import { Objects, TextGens, isDefined, lock } from "@mjtdev/engine";
import { QueryForDataParams } from "./QueryForDataParams";
import { mlOutputParser } from "./mlOutputParser";
import { queryHistoryToText } from "./queryHistoryToText";
import { defaultedParams } from "./defaultedParams";
import { waitFor } from "./waitFor";
import { showAiLog } from "../ui/ai_log/showAiLog";
import { hideAiLog } from "../ui/ai_log/hideAiLog";
import { updateAiLogEntry } from "../ui/ai_log/updateAiLogEntry";
import { addAiLogEntry } from "../ui/ai_log/addAiLogEntry";

export const queryForData = async <T extends Record<string, string>>(
  params: Partial<QueryForDataParams<T>> = {}
): Promise<Record<keyof T, string>> => {
  let {
    filter,
    mapper,
    prompt,
    propertyPrefix,
    returnRawResponse,
    legend,
    start,
    end,
    format,
    user,
    system,
    assistant,
    promptLead,
    attempts,
    context,
    persona,
    textGenParams = {},
  } = defaultedParams(params);

  const firstKey = Object.keys(legend)[0];
  // const firstKey = "confidence";
  promptLead = `${start}\n${propertyPrefix}${firstKey}: `;
  // promptLead = `${start}\n${propertyPrefix}${firstKey}: HIGH\n`;

  // const extendedLegend = { confidence: "confidence in response", ...legend };
  const outputTemplateItems = Object.entries(legend)
    // const outputTemplateItems = Object.entries(extendedLegend)
    .map((e) => `${propertyPrefix}${e[0]}: ${e[1]}`)
    .join("\n");

  const template = [start, outputTemplateItems, end].join("\n");

  console.log(prompt);
  const historyText = queryHistoryToText(params);

  const systemDesc = [
    persona,
    `You answer questions by generating ${format} responses.`,
  ]
    .filter(isDefined)
    .join(" ");
  const renderedPrompt = [
    context,
    `${system} ${systemDesc} Follow this output format:`,
    template,
    historyText,
    `${user} ${prompt}`,
    `${assistant} ${promptLead}`,
  ]
    .filter(isDefined)
    .join("\n");
  const tokens = TextGens.textToTokens(renderedPrompt);
  console.log(`tokens: ${tokens.length}`);
  const aiLogId = addAiLogEntry({
    promptTokenCount: tokens.length,
    prompt,
    type: "text",
  });
  const response = await waitFor(() =>
    lock(() =>
      TextGens.textgen(renderedPrompt, {
        stopping_strings: [user, assistant, system, start, end],
        ...textGenParams,
      })
    )
  );
  updateAiLogEntry({ id: aiLogId, response });
  if (returnRawResponse) {
    return response as unknown as T;
  }
  const fullResponseText = `${propertyPrefix}${firstKey}: ${response}`;
  // const fullResponseText = `${response}`;
  console.log(fullResponseText);
  const parsedResult = Objects.safe(() =>
    mlOutputParser(fullResponseText, propertyPrefix)
  ) as T;
  // console.log({ parsedResult });
  if (isDefined(parsedResult)) {
    const mappedValue = mapper(parsedResult);
    if (filter(mappedValue)) {
      return mappedValue;
    }
  }
  if (attempts > 1) {
    return queryForData({ ...params, attempts: attempts - 1 });
  }
};
