import { Border, Grid, isUndefined } from "@mjtdev/engine";
import { EntityDisplay } from "./EntityDisplay";
import { openExpandEntityPopup } from "./openExpandEntityPopup";
import { useEntitiesByType } from "./useEntitiesByType";
import { EntityType } from "../game/play/EntityType";
import { ButtonGroup } from "./ButtonGroup";
import { openGenerateGroupPopup } from "./openGenerateGroupPopup";
import { findEntities } from "../game/findEntities";

export const EntitiesDisplay = ({ type }: { type: EntityType }) => {
  const groupEntities = useEntitiesByType("group").filter(
    (g) => findEntities((e) => e.parent === g.id && e.type === type).length > 0
  );
  const looseEntities = useEntitiesByType(type).filter((e) =>
    isUndefined(e.parent)
  );
  const contents = [...groupEntities, ...looseEntities].map((thing, i) => (
    <EntityDisplay
      options={{
        imageStyle: { maxHeight: "10em" },
      }}
      key={i}
      entity={thing}
    />
  ));
  return (
    <Border style={{ maxHeight: "80vh", overflow: "auto" }} title={`${type}s`}>
      <ButtonGroup
        actions={{
          [`Add ${type} Group`]: () => openGenerateGroupPopup({ type }),
          [`Add ${type}`]: () => openExpandEntityPopup({ type }),
        }}
      />
      <Grid direction="column" cellSize={"min-content"} count={4}>
        {contents}
      </Grid>
    </Border>
  );
};
