import { safe } from "@mjtdev/engine";
import { queryForData } from "../../ai/queryForData";
import { parseTextToList } from "../../common/parseTextToList";
import { getGameState } from "../useGameState";
import { Person } from "./Person";
import { idToContext } from "./idToContext";
import { personToContext } from "./personToContext";

export const createGoals = async (params: {
  person: Person;
  associationIds: string[];
  prompt: string;
}) => {
  const { persona, context: superContext } = getGameState();

  const { prompt, person, associationIds = [] } = params;
  const characterContext = [person]
    .map((p) => personToContext(p, { associationIds }))
    .join("\n");
  const associatedContexts = associationIds
    .map((id) =>
      idToContext(id, { associationIds: [...associationIds, person.id] })
    )
    .join("\n");

  const resp = await queryForData({
    persona,
    context: [superContext, associatedContexts, characterContext].join("\n"),
    prompt,
    legend: { answer: "the answer" },
    attempts: 3,
    filter: (v) => {
      return safe(() => parseTextToList(v.answer).length > 2);
    },
    textGenParams: {
      max_new_tokens: 1024,
    },
  });
  return parseTextToList(resp.answer);
};
