import { isEmpty } from "../common/isEmpty";
import { Entity } from "./play/Entity";
import { generateEntityDescription } from "./play/generateEntityDescription";
import { updateGameState } from "./useGameState";

export const updateEntityDescription = async (
  entity: Entity,
  options: Partial<{ context: string }> = {}
) => {
  const { context } = options;
  const { prompt, description: _, ...partialEntity } = entity;
  const { prompt: updatedPrompt, description } =
    await generateEntityDescription(
      {
        ...partialEntity,
        description: undefined,
        prompt: isEmpty(prompt) ? undefined : prompt,
      },
      { context }
    );
  updateGameState((state) => {
    const { entities } = state;
    const t = entities.find((t) => t.id === entity.id);
    t.description = description;
    t.prompt = updatedPrompt;
    delete t.values["looksLike"];
    return state;
  });
};
