import {
  Border,
  Center,
  Colors,
  ContentEditable,
  Grid,
  Randoms,
  Strings,
  isDefined,
} from "@mjtdev/engine";
import { useState } from "react";
import { Entity } from "../game/play/Entity";
import { nameOf } from "../game/play/nameOf";
import { updateGameState, useGameState } from "../game/useGameState";
import { ConnectionsDisplay } from "./ConnectionsDisplay";
import { ExpandedEntityDisplay } from "./ExpandedEntityDisplay";
import { RecordDisplay } from "./RecordDisplay";
import { blobToUrl } from "./blobToUrl";
import { openEntityContextMenu } from "./openEntityContextMenu";
import { useEntitiesByType } from "./useEntitiesByType";

export const EntityDisplay = ({
  entity,
  options = {},
}: {
  options?: Partial<{
    imageStyle?: React.CSSProperties;
    textStyle?: React.CSSProperties;
    style?: React.CSSProperties;
    showPrompt: boolean;
    showDescription: boolean;
    showValues: boolean;
    showImage: boolean;
    showBorderImage: boolean;
    showMemories: boolean;
    showGoals: boolean;
  }>;
  entity: Entity;
}) => {
  const { playerId } = useGameState();
  const {
    imageStyle = {},
    style = {},
    textStyle = {},
    showDescription = false,
    showValues = false,
    showPrompt = false,
    showImage = true,
    showBorderImage = !showImage,
    showMemories = false,
    showGoals = false,
  } = options;
  const { image, id } = entity;
  const [expanded, setExpanded] = useState(true);
  const url = blobToUrl(id, image);
  const expandedDisplay = expanded ? (
    <ExpandedEntityDisplay options={options} entity={entity} />
  ) : undefined;

  const connectionsDisplay =
    entity.type === "person" && showGoals ? (
      <ConnectionsDisplay style={textStyle} person={entity} />
    ) : undefined;

  const valuesDisplay = showValues ? (
    <Border
      style={{ maxHeight: "30em", overflow: "auto", ...textStyle }}
      title="values"
    >
      <RecordDisplay record={entity.values} />
      <RecordDisplay
        record={
          Object.fromEntries(
            Object.entries(entity).filter((e) => typeof e[1] === "string")
          ) as Record<string, string>
        }
      />
    </Border>
  ) : undefined;

  const descriptionDisplay = showDescription ? (
    <Border style={{ maxHeight: "30em", overflow: "auto" }} title="description">
      <ContentEditable
        style={{
          ...textStyle,
        }}
        key={entity.id}
        onRef={(r) => (r.innerText = entity.description)}
        onChange={(description) =>
          updateGameState((state) => {
            const { entities } = state;
            const t = entities.find((t) => t.id === entity.id);
            t.description = description;
            return state;
          })
        }
      />
    </Border>
  ) : undefined;

  const promptDisplay = showPrompt ? (
    <Border style={{ maxHeight: "30em", overflow: "auto" }} title="prompt">
      <ContentEditable
        style={{
          ...textStyle,
        }}
        key={entity.id}
        onRef={(r) =>
          isDefined(entity?.prompt) ? (r.innerText = entity.prompt) : undefined
        }
        onChange={(prompt) =>
          updateGameState((state) => {
            const { entities } = state;
            const t = entities.find((t) => t.id === entity.id);
            t.prompt = prompt;
            return state;
          })
        }
      />
    </Border>
  ) : undefined;

  const imageDisplay = showImage ? (
    <Center>
      <img
        title={entity.description}
        onContextMenu={(evt) => openEntityContextMenu(evt, entity)}
        onClick={() => {
          updateGameState({ selectedId: entity.id });
          setExpanded(!expanded);
        }}
        style={imageStyle}
        src={url}
      />
    </Center>
  ) : undefined;

  const titleImageDisplay = showBorderImage ? (
    <Center>
      <img
        title={entity.description}
        onContextMenu={(evt) => openEntityContextMenu(evt, entity)}
        onClick={() => {
          updateGameState({ selectedId: entity.id });
          setExpanded(!expanded);
        }}
        style={{ height: "1em" }}
        src={url}
      />
    </Center>
  ) : undefined;
  const childrenCount = useEntitiesByType(entity.type).filter(
    (e) => e.parent === entity.id
  ).length;

  const markerDisplay = entity.id === playerId ? "*" : "";
  const titleDisplay = (
    <div
      onClick={() => {
        updateGameState({ selectedId: entity.id });
      }}
      onContextMenu={(evt) => openEntityContextMenu(evt, entity)}
      style={{
        maxWidth: textStyle?.maxWidth || "15vw",
        overflow: "hidden",
        textOverflow: "ellipsis",
        whiteSpace: "nowrap",
      }}
    >
      <Grid direction="column" gap={"0.3em"} cellSize={"min-content"}>
        {nameOf(entity)}
        {titleImageDisplay}
        {markerDisplay}
        {`${childrenCount > 0 ? `(${childrenCount})` : ""}`}
      </Grid>
    </div>
  );
  const colorSeed = Strings.hashFnv32a({ str: entity.parent || entity.id });
  const random = Randoms.seedRandom(colorSeed);
  const borderColor = Colors.randomColor(random).toString();

  return (
    <Border
      style={{
        borderColor,
        // color: textColor,
        backgroundColor: Colors.from(borderColor)
          .darken(0.8)
          .alpha(0.8)
          .toString(),
      }}
      collapsable={childrenCount > 0}
      alwaysShowChildren={true}
      onDiscloserChange={(expanded) => {
        setExpanded(expanded);
      }}
      title={titleDisplay}
    >
      <div style={style}>
        <Grid direction="row" cellSize={"min-content"}>
          {imageDisplay}
          {connectionsDisplay}
          {descriptionDisplay}
          {promptDisplay}
          {valuesDisplay}
          {expandedDisplay}
          {/* <MiniPersonAvatars
            persons={persons.filter((p) => p.owns.includes(entity.id))}
          /> */}
        </Grid>
      </div>
    </Border>
  );
};
