import { Border, ContentEditable, Grid, isUndefined } from "@mjtdev/engine";
import { AssetAvatar } from "../game/interaction/AssetAvatar";
import { updatePerson } from "../game/interaction/updatePerson";
import { Person } from "../game/play/Person";
import { nameOfPerson } from "../game/play/nameOfPerson";
import { updateGameState, useGameState } from "../game/useGameState";
import { findPlace } from "./findPlace";
import { openPersonContextMenu } from "./openPersonContextMenu";
import { ConnectionsDisplay } from "./ConnectionsDisplay";
import { RecordDisplay } from "./RecordDisplay";

export const PersonDisplay = ({
  style = {},
  imageStyle = {},
  person,
  options = {},
}: {
  options?: Partial<{
    showLocation: boolean;
    showStats: boolean;
    showDescription: boolean;
    showMemories: boolean;
    showGoals: boolean;
    collapsable: boolean;
    showValues: boolean;
  }>;
  style?: React.CSSProperties;
  imageStyle?: React.CSSProperties;
  person: Person;
}) => {
  if (isUndefined(person)) {
    return <div></div>;
  }
  const { selectedId, playerId } = useGameState();
  const name = nameOfPerson(person);

  const {
    showLocation = false,
    showMemories = false,
    showGoals = false,
    showDescription = false,
    collapsable = false,
    showStats = false,
    showValues = false,
  } = options;
  const avatar = <AssetAvatar style={imageStyle} asset={person} />;

  const valuesDisplay = showValues ? (
    <Border style={{ maxHeight: "30em", overflow: "auto" }} title="values">
      <RecordDisplay record={person.values} />
    </Border>
  ) : undefined;
  const statsDisplay = showStats ? (
    <Border style={{ maxHeight: "20em", overflow: "auto" }} title="stats">
      <div>age: {person.values.age}</div>
      <div>sex: {person.values.sex}</div>
      <div>race: {person.values.race}</div>
      <div>occ: {person.values.occupation}</div>
    </Border>
  ) : undefined;
  // const memoriesDisplay = showMemories ? (
  //   <MemoriesDisplay person={person} />
  // ) : undefined;
  const goalsDisplay = showGoals ? (
    <ConnectionsDisplay person={person} />
  ) : undefined;

  const descriptionDisplay = showDescription ? (
    <Border
      style={{ backgroundColor: "red", maxHeight: "10em", overflow: "auto" }}
      title="description"
    >
      <ContentEditable
        key={person.id}
        style={{ height: "10em", overflow: "auto" }}
        onRef={(r) => {
          r.textContent = person.description;
        }}
        onChange={(value) => {
          console.log(value);
          updatePerson({ ...person, description: value });
        }}
      />
    </Border>
  ) : undefined;

  const locationDisplay = showLocation ? (
    <Border style={{ maxHeight: "20em", overflow: "auto" }} title="location">
      {findPlace(person.values.location)?.name}
    </Border>
  ) : undefined;

  const markerDisplay = person.id === playerId ? "*" : "";
  return (
    <Border
      collapsable={collapsable}
      style={{ overflow: "auto", ...style }}
      title={
        <div
          style={{
            maxWidth: "15em",
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
          }}
        >
          {name}
          {markerDisplay}
        </div>
      }
    >
      <Grid direction="row" cellSize={"min-content"}>
        <div
          onClick={() => {
            updateGameState({ selectedId: person.id });
          }}
          onContextMenu={(evt) => openPersonContextMenu(evt, person)}
        >
          {avatar}
        </div>
        {statsDisplay}
        {valuesDisplay}
        {locationDisplay}
        {/* {memoriesDisplay} */}
        {goalsDisplay}
        {descriptionDisplay}
      </Grid>
    </Border>
  );
};
