import { findEntity } from "../../ui/findEntity";
import { findPerson } from "../../ui/findPerson";
import { entityToContext } from "./entityToContext";
import { personToContext } from "./personToContext";

export const idToContext = (
  id: string,
  options: Partial<{ associationIds: string[] }> = {}
) => {
  const personMaybe = findPerson(id);
  if (personMaybe) {
    return personToContext(personMaybe, options);
  }
  const entity = findEntity(id);
  return entityToContext(entity);
};
