import { Border, Grid } from "@mjtdev/engine";

export const RecordDisplay = ({
  record,
}: {
  record: Record<string, string>;
}) => {
  const contents = Object.entries(record)
    .sort((a, b) => a[0].localeCompare(b[0]))
    .map((entry, i) => {
      const [key, value] = entry;
      return (
        <Border key={i} title={key}>
          <div style={{ maxHeight: "5em", overflow: "auto" }}>{value}</div>
        </Border>
      );
    });
  return (
    <Grid
      gap="1em"
      direction="column"
      // cellSize={["min-content", "1fr"]}
      count={1}
    >
      {contents}
    </Grid>
  );
};
