import { openPopup } from "@mjtdev/engine";
import { GamePallet } from "./GamePallet";

export const openGamePallet = (event: React.MouseEvent) => {
  const name = "game-pallet";
  return openPopup(<GamePallet name={name} />, {
    name,

    x: event.clientX - 3,
    y: event.clientY - 3,
  });
};
