import { Idbs } from "@mjtdev/engine";
import { useEffect, useState } from "react";
import { GAME_DB, loadGame } from "../game/useGameState";
import { saveApp, updateAppState, useAppState } from "./useAppState";

export const LoadGameDDLB = () => {
  const { currentGame } = useAppState();
  const [saves, setSaves] = useState<string[]>([]);
  useEffect(() => {
    Idbs.list(GAME_DB).then((keys) => {
      console.log({ keys });
      setSaves(keys as string[]);
    });
  }, [currentGame]);

  const options = saves.map((s, i) => <option key={i}>{s}</option>);

  return (
    <select
      style={{ backgroundColor: "black", color: "white" }}
      value={currentGame}
      onChange={async (evt) => {
        const name = evt.target.value;
        await loadGame(name);
        updateAppState({ currentGame: name });
        saveApp();
      }}
    >
      {options}
    </select>
  );
};
