import { Randoms, isDefined } from "@mjtdev/engine";
import { QueryHistory, QueryHistoryEntry } from "../../ai/QueryForDataParams";
import { queryForData } from "../../ai/queryForData";
import { Place } from "../Place";
import { GameScene } from "../interaction/GameScene";
import { getTimeOfDay } from "../interaction/getTimeOfDay";
import { getGameState } from "../useGameState";
import { Person } from "./Person";
import { nameOfPerson } from "./nameOfPerson";
import { personToContext } from "./personToContext";
import { isNotEmpty } from "../../common/isNotEmpty";
import { intersect } from "./intersect";
import { unique } from "../interaction/unique";
import { findTribe } from "./findTribe";
import { tribeToContext } from "../tribeToContext";
import { uniqueHistory } from "./uniqueHistory";
import { findEntitiesByType } from "../../ui/findEntitiesByType";

export const personsToFamilyContext = (persons: Person[]) => {
  const families = unique(persons.map((p) => p.parent)).map((f) =>
    findTribe(f)
  );
  return families.map(tribeToContext).join("\n");
};

export const makeScene = async (
  options: Partial<{
    title: string;
    instruction: string;
    context: string;
    persona: string;
    location: Place;
    history: QueryHistory;
    persons: Person[];
  }>
) => {
  const places = findEntitiesByType("place");
  const {
    title,
    persona,
    persons = [],
    history = [],
    context: superContext = "",
    instruction = "the next multiple paragraph detailed scene",
    location = Randoms.pickRandom(places),
  } = options;

  const associationIds = [...persons.map((p) => p.id), location.id];
  const characterContext = persons
    .map((p) => personToContext(p, { associationIds }))
    .join("\n");
  const legend = {
    scene: "The scene",
  };

  const textPrompt = `USE SHOW DON'T TELL! In the style of a master novel writer. Write ${instruction}, a scene involving ${persons
    .map((p) => nameOfPerson(p))
    .join(" and ")}. Write from ${nameOfPerson(
    persons[0]
  )}'s 2nd person point of view. Use show don't tell. Be brief. Write only the immediate moments of scene, do not foreshadow future events. Use the current moment and location of ${
    location.name
  }, ${getTimeOfDay()}.`;

  const familyContext = personsToFamilyContext(persons);
  const context = [
    superContext,
    familyContext,
    characterContext,
    `Characters in the scene: ${persons.map(nameOfPerson).join(",")}`,
  ].join("\n");

  const relationsHistory = Randoms.pickRandoms({
    from: persons[0].connections.filter(
      (m) => intersect(m.associationIds, associationIds).length > 0
    ),
    count: 5,
    forceUnique: true,
  })
    .filter(isDefined)
    .map((m) => m.history)
    .flat();

  const combinedHistory = uniqueHistory(relationsHistory, history);

  const textResponse = await queryForData({
    persona,
    prompt: textPrompt,
    context,
    history,
    // history: combinedHistory,
    legend,
    textGenParams: {
      max_new_tokens: 1024,
      repetition_penalty: 1.2,
      temperature: 0.8,
    },
    attempts: 3,
    filter: (r) => isNotEmpty(r?.scene?.trim()),
  });

  const textHistoryEntry: QueryHistoryEntry = {
    prompt: textPrompt,
    response: {
      answer: textResponse.scene,
    },
  };

  // const optionsList = await getInteractionsList(persons[1].id, {
  //   history: [textHistoryEntry],
  // });

  const scene: GameScene = {
    place: location,
    title,
    background: undefined,
    persons: persons,
    // options: optionsList,
    options: [],
    text: textResponse.scene,
    history: combinedHistory,
    lastHistoryEntry: textHistoryEntry,
  };
  return scene;
};
