import { isUndefined } from "@mjtdev/engine";
import { Entity } from "../game/play/Entity";
import { getGameState } from "../game/useGameState";

export type idOrEntityFilter = string | ((entity: Entity) => boolean);

export const findEntity = (idOrFilter: idOrEntityFilter) => {
  if (isUndefined(idOrFilter)) {
    return undefined;
  }
  const { entities } = getGameState();
  const filter =
    typeof idOrFilter === "string"
      ? (e: Entity) => e.id === idOrFilter
      : idOrFilter;
  return entities.find(filter);
};
