import {
  Objects,
  Randoms,
  isDefined,
  isUndefined,
  openContextMenu,
} from "@mjtdev/engine";
import { Person } from "../game/play/Person";
import { updatePersonImage } from "../game/updatePersonImage";
import { updateGameState } from "../game/useGameState";
import { findPlace } from "./findPlace";
import { getPlayer } from "./getPlayer";
import { getGoals } from "../game/play/getGoals";
import { startGoallInteraction } from "./startGoalInteraction";
import { generatePersonTalksLike } from "../game/play/generatePersonTalksLike";
import { updatePerson } from "../game/interaction/updatePerson";
import { updatePersonTalksLike } from "../game/updatePersonTalksLike";
import { startInteraction } from "../game/interaction/startInteraction";
import { openFormPopup } from "./form/openFormPopup";
import { nameOf } from "../game/play/nameOf";
import { waitFor } from "../ai/waitFor";

export const getGoalInteractions = async (person: Person) => {
  const player = getPlayer();
  if (isUndefined(player)) {
    return {};
  }
  const goals = await waitFor(() =>
    getGoals(player, [person.id, person.values.location].filter(isDefined))
  );
  const goalInteractions = Objects.fromEntries(
    goals.map(
      (goal) =>
        [
          goal.value,
          async () => {
            return startGoallInteraction(goal, {
              location: findPlace(person.values.location),
              personIds: [person.id],
            });
          },
        ] as const
    )
  );
  return goalInteractions;
};

export const openPersonContextMenu = async (
  evt: React.MouseEvent,
  person: Person
) => {
  evt.preventDefault();
  const goalInteractions = await getGoalInteractions(person);
  openContextMenu(evt, {
    ...goalInteractions,
    updateImage: () => {
      updatePersonImage(person);
    },
    updateTalksLike: async () => {
      updatePersonTalksLike(person);
    },
    become: () => {
      updateGameState({ playerId: person.id });
    },
    interactWith: async () => {
      const player = getPlayer();
      const resp = await openFormPopup({
        title: "interaction",
        fields: [
          {
            name: "choice",
          },
        ],
      });
      if (isUndefined(resp)) {
        return;
      }

      const instruction = `${nameOf(player)} chose to ${resp.choice}`;
      const scenes = await startInteraction({
        title: resp.choice,
        instruction,
        // history: goal.history,
        personIds: [player.id, person.id],
        place: findPlace(person.values.location),
        // place: location,
      });
    },
    randomInteraction: async () => {
      const player = getPlayer();
      const goals = await getGoals(player, [person.id, person.values.location]);
      const goal = Randoms.pickRandom(goals);
      return startGoallInteraction(goal, {
        location: findPlace(person.values.location),
        personIds: [person.id],
      });
    },
  });
};
