import { Person } from "./play/Person";
import { generatePersonTalksLike } from "./play/generatePersonTalksLike";
import { updateGameState } from "./useGameState";

export const updatePersonTalksLike = async (person: Person) => {
  const talksLike = await generatePersonTalksLike(person);
  updateGameState((state) => {
    const p = state.entities.find((p) => p.id === person.id);
    p.values["talksLike"] = talksLike;
    return state;
  });
};
