import { ImageGens, TextGens, isUndefined } from "@mjtdev/engine";
import { openFormPopup } from "./form/openFormPopup";
import { getAppState, saveApp, updateAppState } from "./useAppState";
import { setupAi } from "../ai/setupAi";

export const openAppSettingsPopup = async () => {
  const { currentGame, ...settings } = getAppState();

  const fields = Object.entries(settings)
    .sort((a, b) => a[0].localeCompare(b[0]))
    .map(([key, value]) => ({ name: key, value }));

  console.log("WTF");
  const resp = await openFormPopup({
    title: "App Settings",
    fields,
  });
  console.log({ resp });
  if (isUndefined(resp)) {
    return;
  }
  updateAppState({ ...resp });
  setupAi();

  saveApp();
  console.log({ tg: TextGens.useTextGenState.getState() });
  console.log({ ig: ImageGens.useImageGenState.getState() });
};
