import { Objects } from "@mjtdev/engine";
import { parseTextToList } from "../common/parseTextToList";


export const csvTextToRecords = (text: string, fieldNames: string[]) => {
  const list = parseTextToList(text);
  return list.map((line) => {
    const fields = line
      .split(",")
      .map((x) => x.trim().replace(/^/, "").replace(/"$/, ""));
    const entries = fieldNames.map((name, i) => [name, fields[i]] as const);
    return Objects.fromEntries(entries);
  });
};
