import { hideAiLog } from "../ui/ai_log/hideAiLog";
import { showAiLog } from "../ui/ai_log/showAiLog";
import { updatePersonLocations } from "../ui/updatePersonLocations";
import { saveApp, updateAppState } from "../ui/useAppState";
import { DEFAULT_GAME_PARAMS } from "./DEFAULT_GAME_PARAMS";
import { clearGameState } from "./clearGameState";
import { generateEntities } from "./generateEntities";
import { generateLore } from "./generateLore";
import { generateLores } from "./generateLores";
import { makeConnection } from "./makeConnection";
import { entityToContext } from "./play/entityToContext";
import { generateGroup } from "./play/generateGroup";
import { updateEntityImages } from "./updateEntityImages";
import { saveGame, updateGameState } from "./useGameState";

export type NewGameOptions = {
  name: string;
  historicalEventCount: number;
  placesPerHistoricalEventCount: number;
  personsPerPlaceCount: number;
  historicalItemsCount: number;
  persona: string;
  context: string;
};

export const startNewGame = async (options: Partial<NewGameOptions> = {}) => {
  console.log("starting new game!");
  console.log({ options });

  const {
    name,
    persona,
    context,
    historicalEventCount = 3,
    placesPerHistoricalEventCount = 3,
    historicalItemsCount = 3,
    personsPerPlaceCount = 3,
  } = options;

  clearGameState();

  updateGameState({
    entities: [],
    time: new Date().toISOString(),
    seed: Date.now(),
    scenes: [],
    ...DEFAULT_GAME_PARAMS,
    persona,
    context,
  });
  updateAppState({ currentGame: name });
  saveGame(name);
  saveApp();

  showAiLog();
  const historyLore = await generateLore({
    kind: "history",
    prompt:
      "a complete history for the story, with details of important historical events within the story",
  });
  updateGameState((state) => {
    state.entities.push(historyLore);
    return state;
  });
  const historicalEvents = await generateLores({
    count: historicalEventCount,
    parent: historyLore,
    kind: "historical event",
    hint: "unique events within the history of the story",
  });

  updateGameState((state) => {
    state.entities.push(...historicalEvents);
    return state;
  });

  const places = (
    await Promise.all(
      historicalEvents.map(async (historicalEvent) => {
        return generateEntities({
          count: placesPerHistoricalEventCount,
          type: "place",
          connections: [
            makeConnection({
              type: "lore",
              associationIds: [historicalEvent.id],
              value: "generated from historical event",
            }),
          ],
        });
      })
    )
  ).flat();
  updateGameState((state) => {
    state.entities.push(...places);
    return state;
  });

  const items = (
    await Promise.all(
      historicalEvents.map(async (historicalEvent) => {
        return generateEntities({
          count: historicalItemsCount,
          type: "thing",
          hint: "item of historical significance within the context of the story",
          connections: [
            makeConnection({
              type: "lore",
              associationIds: [historicalEvent.id],
              value: "generated from historical event",
            }),
          ],
        });
      })
    )
  ).flat();
  updateGameState((state) => {
    state.entities.push(...items);
    return state;
  });

  // TODO tribes per place
  for (let place of places) {
    const connection = makeConnection({
      type: "lore",
      associationIds: [place.id],
      value: "generated from place",
    });
    await generateGroup({
      type: "person",
      context: entityToContext(place),
      connections: [connection],
      count: personsPerPlaceCount,
    });
  }
  updatePersonLocations();
  // await updateEntityImages();
  updateGameState((state) => {
    state.playerId = state.entities.find((e) => e.type === "person")?.id;
    return state;
  });
  await updateEntityImages();
  saveGame(name);
  saveApp();
  hideAiLog();
};
