import { Lore } from "./Lore";
import { findEntities } from "./findEntities";
import { findSiblings } from "./findSiblings";
import { entityToContext } from "./play/entityToContext";

export const getFlakeDescriptionContext = (flake: Partial<Lore>) => {
  const context = findEntities((e) => e.type === "group")
    .map((e) => entityToContext(e))
    .join("\n");

  return context;
};
