import { Border, ContentEditable, isUndefined } from "@mjtdev/engine";
import { FormField } from "./FormField";

export const FieldDisplay = <V extends string | boolean>({
  autoFocus,
  field,
  onChange = () => {},
}: {
  autoFocus: boolean;
  onChange?: (value: V) => void;
  field: FormField<any, any>;
}) => {
  const editableDispaly =
    typeof field.value === "string" || isUndefined(field.value) ? (
      <ContentEditable
        key={field.name}
        value={field.value}
        autoFocus={autoFocus}
        style={{
          paddingLeft: "1em",
          paddingRight: "2em",
          minWidth: "50vw",
        }}
        onChange={(v) => onChange(v as V)}
        // onChange={(value) => {
        //   console.log({ value, field });
        //   field.value = isEmpty(value) ? undefined : value;
        //   console.log({ after: true, value, field });
        // }}
      />
    ) : (
      <input
        defaultChecked={field.value}
        // onChange={(evt) => (field.value = evt.target.checked)}
        onChange={(evt) => onChange(evt.target.checked as V)}
        type="checkbox"
      />
    );
  return <Border title={String(field.name)}>{editableDispaly}</Border>;
};
