import { Asserts, isDefined, isUndefined } from "@mjtdev/engine";
import { Lore } from "../game/Lore";
import { generateEntities } from "../game/generateEntities";
import { Entity } from "../game/play/Entity";
import { updateGameState } from "../game/useGameState";
import { openFormPopup } from "./form/openFormPopup";
import { openExpandLorePopup } from "./openExpandLorePopup";
import { EntityType } from "../game/play/EntityType";
import { updateEntityImages } from "../game/updateEntityImages";
import { Connection } from "../game/play/Connection";

export const openExpandEntityPopup = async <T extends EntityType>(
  params: Partial<{
    context?: string;
    parent: Entity;
    type?: T;
    connections?: Connection[];
  }>
) => {
  const { parent, type = parent?.type, connections, context } = params;
  Asserts.assertValue(type);
  console.log({ params });
  if (parent?.type === "lore") {
    return openExpandLorePopup(parent as Lore);
  }

  const resp = await openFormPopup({
    title: `Expand Upon ${isDefined(parent) ? parent.name : type}`,
    fields: [
      {
        name: "prompt",
        value: `specific ${type}s`,
      },
      { name: "count", value: "3" },
    ],
  });
  if (isUndefined(resp)) {
    return;
  }

  const hint = isDefined(parent)
    ? `${resp.prompt} within ${parent.name}`
    : resp.prompt;

  console.log({ parent });
  const entities = await generateEntities({
    count: Number(resp.count),
    parent,
    connections,
    context,
    type,
    hint,
  });
  updateGameState((state) => {
    state.entities.push(...entities);
    return state;
  });
  await updateEntityImages();
};
