import { BrowserFiles, isUndefined } from "@mjtdev/engine";
import { isEmpty } from "../common/isEmpty";
import { openFormPopup } from "../ui/form/openFormPopup";
import { importGameFromJsonString } from "./importGameFromJsonString";

export const importGame = async () => {
  const files = await BrowserFiles.openFileHandle({
    multiple: false,
  });
  if (isUndefined(files) || files.length !== 1) {
    return;
  }
  const [file] = files;
  const resp = await openFormPopup({
    title: "Import As",
    fields: [{ name: "name", value: "Imported" }],
  });
  if (isEmpty(resp?.name)) {
    return;
  }
  const rawJsonString = await (await file.getFile()).text();
  return importGameFromJsonString(rawJsonString, resp.name);
};
