import { Htmls, ImageGens, Objects, isUndefined, lock } from "@mjtdev/engine";

import { Caches } from "@mjtdev/engine";
import { StableDiffusionProcessingTxt2Img } from "@mjtdev/engine/packages/mjtdev-ai/src/imagegen/api/AutomaticApi";
import { getGameState } from "../game/useGameState";
import { addAiLogEntry } from "../ui/ai_log/addAiLogEntry";

const PROMPT_IMAGE_CACHE =
  Caches.create<Promise<HTMLCanvasElement>>("PromptImageCache");

export const drawImagePrompt = async (
  props: Partial<StableDiffusionProcessingTxt2Img> & {
    alpha?: number;
    canvas: HTMLCanvasElement;
    prompt: string;
  }
) => {
  return lock(async () => {
    const { seed: gameSeed } = getGameState();
    const { prompt, seed = gameSeed, canvas, alpha = 1, steps = 20 } = props;
    if (isUndefined(canvas)) {
      return;
    }
    const aiLogId = addAiLogEntry({ prompt, type: "image" });

    const key = [prompt, seed, steps].join(",");
    const cachedCanvas = await PROMPT_IMAGE_CACHE.get(key, async () => {
      const cvs = Htmls.from("canvas", {
        attributes: { width: canvas.width, height: canvas.height },
      });
      const ctx = cvs.getContext("2d");
      ctx.globalAlpha = alpha;

      await ImageGens.drawTxt2Img(ctx, {
        prompt,
        steps,
        seed,
        restore_faces: true,
        width: cvs.width,
        height: cvs.height,
        ...Objects.omit({ ...props }, "canvas"),
      });
      return cvs;
    });

    const ctx = canvas.getContext("2d");
    ctx.drawImage(cachedCanvas, 0, 0, canvas.width, canvas.height);
  });
};
