import { BrowserFiles, Bytes, isDefined } from "@mjtdev/engine";
import { getGameState } from "./useGameState";
import { resolvePromiseMap } from "./resolvePromiseMap";


export const exportGame = async () => {
  const game = getGameState();
  const promises: Record<string, Promise<string>> = {};
  const blobToIdReplacer = (key: unknown, value: unknown) => {
    if (value instanceof Blob) {
      const id = "blob-to-dataUrl:" + crypto.randomUUID();
      promises[id] = Bytes.toDataUrl(value);
      // promises[id] = Promise.resolve("fuck");
      return id;
    }
    return value;
  };

  const intermediateString = JSON.stringify(game, blobToIdReplacer);
  const intermediateObject = JSON.parse(intermediateString);
  const idToBase64Map = await resolvePromiseMap(promises);
  const idToBase64Replacer = (key: unknown, value: unknown) => {
    if (typeof value === "string") {
      const base64 = idToBase64Map[value];
      if (isDefined(base64)) {
        return base64;
      }
    }
    return value;
  };
  const exportString = JSON.stringify(
    intermediateObject,
    idToBase64Replacer,
    2
  );
  return BrowserFiles.writeFileBrowser("game.json", exportString);
};


