import { Randoms, isDefined } from "@mjtdev/engine";
import { findEntitiesByType } from "../../ui/findEntitiesByType";
import { findPlace } from "../../ui/findPlace";
import { startGoallInteraction } from "../../ui/startGoalInteraction";
import { Person } from "../play/Person";
import { getGoals } from "../play/getGoals";

export const startRandomInteraction = async (
  player: Person = Randoms.pickRandom(findEntitiesByType("person"))
) => {
  const persons = findEntitiesByType("person");

  const nonPlayerPersons = persons.filter((p) => p.id !== player.id);
  const person = Randoms.pickRandom(nonPlayerPersons);

  const goals = await getGoals(
    player,
    [person.id, person.values.location].filter(isDefined)
  );
  const goal = Randoms.pickRandom(goals);
  return startGoallInteraction(goal, {
    player,
    location: findPlace(person.values.location),
    personIds: [person.id].filter(isDefined),
  });
};
