import { isDefined } from "@mjtdev/engine";
import { drawEntityImage } from "./drawEntityImage";
import { Person } from "./play/Person";
import { updateGameState } from "./useGameState";

export const updatePersonImage = async (person: Person) => {
  const { age = "", sex = "", race = "", occupation = "" } = person.values;
  const ageDesc = isDefined(age) ? `age: ${age}` : "";
  const image = await drawEntityImage(person, {
    midStyle: `a ${ageDesc} ${race} ${sex} ${occupation}`,
  });
  updateGameState((state) => {
    const p = state.entities.find((p) => p.id === person.id);
    p.image = image;
    return state;
  });
};
