import { EntityType } from "../game/play/EntityType";
import { getGameState } from "../game/useGameState";
import { TypedEntity } from "./TypedEntity";

export const findEntitiesByType = <T extends EntityType>(
  type: T
): TypedEntity<T>[] => {
  const { entities } = getGameState();
  return entities.filter((e) => e.type === type) as TypedEntity<T>[];
};
