import { isDefined } from "@mjtdev/engine";
import { queryForData } from "../../ai/queryForData";
import { getGameState } from "../useGameState";
import { Entity } from "./Entity";
import { entityToContext } from "./entityToContext";

export const queryEntity = async ({
  entity,
  prompt,
  context,
}: {
  context?: string;
  prompt: string;
  entity: Partial<Entity>;
}) => {
  const { context: gameContext, persona } = getGameState();
  const entityContext = entityToContext(entity);
  const resp = await queryForData({
    prompt,
    context: [gameContext, context, entityContext].filter(isDefined).join("\n"),
    persona,
    legend: { answer: "the answer" },
    textGenParams: {
      repetition_penalty: 1.15,
      temperature: 0.8,
      max_new_tokens: 1024,
    },
  });
  return resp.answer;
};
