import { TypedEntity } from "../ui/TypedEntity";
import { makeEntity } from "./makeEntity";
import { Entity } from "./play/Entity";
import { EntityType } from "./play/EntityType";
import { generateEntityDescription } from "./play/generateEntityDescription";

export const generateEntity = async <T extends EntityType>(
  prototype: { type: T } & Partial<Entity>
): Promise<TypedEntity<T>> => {
  const entity = makeEntity<T>(prototype);
  const expandedValues = entity.type === "place" ? { location: entity.id } : {};

  const { description, prompt } = await generateEntityDescription(prototype);
  return {
    ...entity,
    prompt,
    description,
    values: { ...(entity?.values || {}), ...expandedValues },
  };
};
