import { updateGameState } from "../useGameState";
import { Person } from "./Person";
import { intersect } from "./intersect";
import { generateGoals } from "./generateGoals";
import { Goal } from "./Connection";
import { Asserts } from "@mjtdev/engine";

export const getGoals = async (
  person: Person,
  associationIds: string[]
): Promise<Goal[]> => {
  Asserts.assertValue(person);
  const { connections = [] } = person;
  const found = connections
    .filter((r) => r.type === "goal")
    .filter(
      (g) =>
        intersect(g.associationIds, associationIds).length >=
        associationIds.length
    ) as Goal[];
  if (found.length === 0) {
    const generatedGoals = await generateGoals(person, associationIds);
    updateGameState((state) => {
      const p = state.entities.find(
        (p) => p.id === person.id && p.type === "person"
      ) as Person;
      p.connections.push(...generatedGoals);
      return state;
    });
    return generatedGoals;
  }
  return found;
};
