import { Randoms, isDefined, isUndefined } from "@mjtdev/engine";
import { findEntity } from "../../ui/findEntity";
import { Entity } from "./Entity";
import { nameOf } from "./nameOf";
import { recordToContext } from "./recordToContext";
import { unique } from "../interaction/unique";
import { isNotEmpty } from "../../common/isNotEmpty";
import { intersect } from "./intersect";

export const entityToContext = (
  entity: Partial<Entity>,

  options: Partial<{
    includeLore: boolean;
    includeMemory: boolean;
    associationIds: string[];
  }> = {}
) => {
  const {
    includeLore = true,
    includeMemory = true,
    associationIds = [],
  } = options;
  if (isUndefined(entity)) {
    return undefined;
  }
  const parentCotnext = entityToContext(findEntity(entity?.parent));
  const name = nameOf(entity);

  const { values = {}, connections = [] } = entity;

  const lore = includeLore
    ? unique(
        Randoms.pickRandoms({
          from: connections.filter((r) => r.type === "lore"),
          count: 2,
          forceUnique: true,
        })
          .filter(isDefined)
          .map((r) => Randoms.pickRandom(r.associationIds))
          .map((id) => findEntity(id).description)
          .filter(isNotEmpty)
      ).join(",")
    : undefined;

  const memory = includeMemory
    ? unique(
        Randoms.pickRandoms({
          from: connections
            .filter((r) => r?.type === "memory")
            .filter(
              (m) => intersect(m.associationIds, associationIds).length > 0
            ),
          count: 5,
          forceUnique: true,
        })
          .filter(isDefined)
          .map((m) => m.value)
          .filter(isNotEmpty)
      ).join(",")
    : undefined;

  const valueContext = recordToContext({ ...values, memory, lore });
  const basicContext = [`# ${name}`, entity.description].join(": ");
  const context = [basicContext, valueContext].filter(isDefined).join("\n");
  return [parentCotnext, context].filter(isDefined).join("\n");
};
