import { Person } from "./Person";
import { Goal } from "./Connection";
import { createGoals } from "./createQuests";
import { nameOf } from "./nameOf";
import { nameOfPerson } from "./nameOfPerson";

export const generateGoals = async (
  person: Person,
  associationIds: string[]
) => {
  console.log({ person, associationIds });
  const associationNames = associationIds.map(nameOf).join(" and ");
  const prompt = `Be terse. Use as few words as possible. Using first person perspective (eg. my, I) as ${nameOfPerson(
    person
  )}. Create a numbered bullet point list of 5 near term goals for ${nameOfPerson(
    person
  )} which can only be achieved with ${associationNames}`;
  const goalValues = await createGoals({
    person,
    associationIds,
    prompt,
  });
  const goals: Goal[] = goalValues.map((value) => ({
    id: crypto.randomUUID(),
    value,
    type: "goal",
    associationIds,
    history: [],
  }));
  return goals;
};
