import { updateGameState } from "../useGameState";
import { Person } from "../play/Person";

export const updatePerson = (person: Partial<Person>) => {
  console.log("updating person")
  console.log(person)
  updateGameState((state) => {
    // const { persons } = state;
    const persons = state.entities.filter(
      (e) => e.type === "person"
    ) as Person[];
    const pIdx = persons.findIndex((p) => p.id === person.id);
    const p = persons[pIdx];

    persons[pIdx] = { ...p, ...person };
    return state;
  });
};
