import { isUndefined } from "@mjtdev/engine";
import { Asset } from "../asset/Asset";
import { blobToUrl } from "../../ui/blobToUrl";
import { CSSProperties } from "react";

export const AssetAvatar = ({
  style,
  asset,
}: {
  style?: CSSProperties;
  asset: Asset;
}) => {
  const { id, image } = asset;
  const url = blobToUrl(id, image);

  return <img style={style} src={url} />;
};
