import { isDefined, isUndefined } from "@mjtdev/engine";
import { isEmpty } from "../../common/isEmpty";

export const recordToContext = (record: object) => {
  if (isUndefined(record)) {
    return undefined;
  }
  return Object.entries(record)
    .filter((e) => isDefined(e[1]))
    .map((e) => {
      if (typeof e[1] !== "string" || isEmpty(e[1])) {
        return undefined;
      }
      return `- ${e[0]}: ${e[1]}`;
    })
    .filter(isDefined)
    .join("\n");
};
