import { getGameState } from "../useGameState";
import { nameOfPerson } from "../play/nameOfPerson";
import { personToContext } from "../play/personToContext";
import { getPlayer } from "../../ui/getPlayer";
import { queryForData } from "../../ai/queryForData";
import { parseTextToList } from "../../common/parseTextToList";
import { findPerson } from "../../ui/findPerson";
import { getGameContext } from "./getGameContext";
import { getTimeOfDay } from "./getTimeOfDay";
import { QueryHistory } from "../../ai/QueryForDataParams";
import { Place } from "../Place";
import { Randoms } from "@mjtdev/engine";
import { findEntitiesByType } from "../../ui/findEntitiesByType";

export const getInteractionsList = async (
  personId: string,
  options: Partial<{ history: QueryHistory; location: Place }> = {}
) => {
  const { persona } = getGameState();
  const places = findEntitiesByType("place");
  const { history = [], location = Randoms.pickRandom(places) } = options;
  const player = getPlayer();
  const person = findPerson(personId);
  const gameContext = getGameContext();
  const playerContext = personToContext(player);
  const personContext = personToContext(person);
  const context = [gameContext, playerContext, personContext].join("\n");
  const prompt = `As a numbered bullet point list, using only what you currently know. Do not repeat prior interactions. Create a list of specific single word gameplay interactions ${nameOfPerson(
    player
  )} can have towards ${nameOfPerson(
    person
  )} in the current moment and location of ${
    location.name
  }, ${getTimeOfDay()}.`;
  const resp = await queryForData({
    prompt,
    context,
    persona,
    // history: [...person.history, ...history],
    history,
    legend: {
      answer: "The answer",
    },
    textGenParams: {
      // repetition_penalty: 1.2,
      temperature: 0.8,
    },
  });
  return parseTextToList(resp.answer);
};
