import { isUndefined } from "@mjtdev/engine";
import { Connection } from "../game/play/Connection";
import { EntityType } from "../game/play/EntityType";
import { generateGroup } from "../game/play/generateGroup";
import { openFormPopup } from "./form/openFormPopup";

export const openGenerateGroupPopup = async (
  params: {
    type: EntityType;
  } & Partial<{
    connections: Connection[];
    context: string;
  }>
) => {
  const { context, connections, type } = params;
  const resp = await openFormPopup({
    title: `Make a Group of ${type}s`,
    fields: [
      { name: "name" },
      { name: "kind" },
      // { name: "prompt" },
      { name: "description" },
      { name: "count", value: "3" },
    ],
  });
  if (isUndefined(resp)) {
    return;
  }

  const { count, ...prototype } = resp;
  return generateGroup({ type, connections, prototype, count, context });
};
