import { Idb, Idbs, createState } from "@mjtdev/engine";

export type AppState = ReturnType<(typeof useAppState)["getState"]>;

export const APP_DB: Idb<AppState> = {
  dbName: "rpg-engine",
  storeName: "app",
};

export const [useAppState, updateAppState] = createState({
  currentGame: "current",
  textGenBaseUrl: "http://gamepc.local:5000",
  imageGenBaseUrl: "http://gamepc.local:8888",
  debug: false,
});

export const getAppState = () => useAppState.getState();

export const saveApp = (key = "current") => {
  const state = useAppState.getState();
  return Idbs.put(APP_DB, key, state);
};

export const loadApp = async (key = "current") => {
  const state = await Idbs.get(APP_DB, key, () => ({
    currentGame: "current",
  }));
  updateAppState(state);
  return state;
};
