import { TypedEntity } from "../ui/TypedEntity";
import { Entity } from "./play/Entity";
import { EntityType } from "./play/EntityType";

export const makeEntity = <T extends EntityType>(
  prototype: { type: T } & Partial<Entity>
): TypedEntity<T> => {
  const {
    connections = [],
    description,
    id = crypto.randomUUID(),
    name,
    parent,
    type,
    values = {},
    image,
    kind,
    prompt,
  } = prototype;

  return {
    connections,
    description,
    id,
    name,
    parent,
    type,
    values,
    image,
    kind,
    prompt,
  } as TypedEntity<T>;
};
