import { isDefined } from "@mjtdev/engine";
import { queryForData } from "../../ai/queryForData";
import { tribeToContext } from "../tribeToContext";
import { getGameState } from "../useGameState";
import { Person } from "./Person";
import { findTribe } from "./findTribe";
import { nameOfPerson } from "./nameOfPerson";
import { personToContext } from "./personToContext";


export const generatePersonTalksLike = async (person: Person) => {
  const { context, persona } = getGameState();
  const characterContext = personToContext(person);
  const family = findTribe(person.parent);
  const familyContext = tribeToContext(family);
  const prompt = `create a brief, terse, description of what ${nameOfPerson(
    person
  )} talks like with examples.`;
  const resp = await queryForData({
    prompt,
    context: [context, familyContext, characterContext]
      .filter(isDefined)
      .join("\n"),
    persona,
    legend: { answer: "the answer" },
    textGenParams: { max_new_tokens: 1024 },
  });
  return resp.answer;
};
