import { Randoms } from "@mjtdev/engine";
import { updateGameState } from "../game/useGameState";
import { Person } from "../game/play/Person";
import { Place } from "../game/Place";

export const updatePersonLocations = () => {
  updateGameState((state) => {
    const persons = state.entities.filter(
      (e) => e.type === "person"
    ) as Person[];
    const places = state.entities.filter((e) => e.type === "place") as Place[];
    for (let person of persons) {
      person.values.location = Randoms.pickRandom(places).id;
    }
    return state;
  });
};


