import { isUndefined, openCenteredPopup } from "@mjtdev/engine";
import { isEmpty } from "../../common/isEmpty";
import { PopupForm } from "./PopupForm";
import { PopupFormDisplay } from "./PopupFormDisplay";
import { FieldDisplay } from "./FieldDisplay";
import { FormField } from "./FormField";

export const recordToFields = <
  V extends string | boolean = string,
  K extends string | number | symbol = string
>(
  record: Record<K, V>
): FormField<K, V>[] => {
  if (isUndefined(record)) {
    return [];
  }
  return Object.entries(record).map(([key, value]) => ({
    name: key as K,
    value: value as V,
  }));
};

export const openFormPopup = <
  V extends string | boolean = string,
  K extends string | number | symbol = string
>({
  title = "Form",
  record,
  fields = recordToFields(record),
}: Partial<PopupForm<K, V>> = {}): Promise<Record<K, V>> => {
  const realizedFields = fields.map((f) => ({
    name: f.name,
    value: isEmpty(f?.value) ? undefined : f.value,
  }));
  const result = realizedFields.reduce((acc, cur) => {
    acc[cur.name as K] = cur.value;
    return acc;
  }, {} as Record<K, V>);
  return new Promise((resolve, reject) => {
    {
      const fieldComponents = realizedFields.map((f, i) => (
        <FieldDisplay<V>
          onChange={(value) => {
            result[f.name] = value;
          }}
          field={f}
          key={i}
          autoFocus={i === 0}
        />
      ));

      const name = crypto.randomUUID();

      openCenteredPopup(
        <PopupFormDisplay
          onSubmit={(ok) => {
            if (!ok) {
              resolve(undefined);
            }
            resolve(result);
          }}
          name={name}
          title={title}
          fieldComponents={fieldComponents}
        ></PopupFormDisplay>,
        {
          name,
        }
      );
    }
  });
};
