import { startInteraction } from "../game/interaction/startInteraction";
import { Person } from "../game/play/Person";
import { Goal } from "../game/play/Connection";
import { nameOfPerson } from "../game/play/nameOfPerson";
import { updateGameState } from "../game/useGameState";
import { findPlace } from "./findPlace";
import { getPlayer } from "./getPlayer";
import { Place } from "../game/Place";

export const startGoallInteraction = async (
  goal: Goal,
  options: Partial<{
    player: Person;
    location: Place;
    personIds: string[];
  }> = {}
) => {
  const {
    player = getPlayer(),
    location = findPlace(player.values.location),
    personIds = [],
  } = options;
  const instruction = `${nameOfPerson(player)} chose to ${goal.value}`;

  const scenes = await startInteraction({
    title: goal.value,
    instruction,
    history: goal.history,
    personIds: [player.id, ...personIds],
    place: location,
  });
  const goalHistory = scenes.map((s) => s.lastHistoryEntry);
  updateGameState((state) => {
    const p = state.entities.find(
      (p) => p.id === player.id && p.type === "person"
    ) as Person;
    const g = p.connections
      .filter((r) => r.type === "goal")
      .find((g) => g.id === goal.id);
    g.history.push(...goalHistory);
    return state;
  });
  console.log({ scenes });
};
