import { isUndefined } from "@mjtdev/engine";
import { getGameState, updateGameState } from "../game/useGameState";
import { openFormPopup } from "./form/openFormPopup";

export const openGameSettingsPopup = async () => {
  const { persona, context, entityPreStyle, imagePostStyle, imagePreStyle } =
    getGameState();
  const resp = await openFormPopup({
    title: "Game Settings",
    record: { persona, context, entityPreStyle, imagePreStyle, imagePostStyle },
  });
  if (isUndefined(resp)) {
    return;
  }
  updateGameState({ ...resp });
};
