import { isDefined } from "@mjtdev/engine";
import { findPlace } from "../../ui/findPlace";
import { findPerson } from "../../ui/findPerson";
import { findEntity } from "../../ui/findEntity";

export const idToImage = (id: string): Blob => {
  const person = findPerson(id);
  if (isDefined(person)) {
    person.image;
  }
  const location = findPlace(id);
  if (isDefined(location)) {
    return location.image;
  }
  return findEntity(id)?.image;
};
