export const generateGroupFrom = (entity: Entity, type: EntityType) => {
  const context = entityToContext(entity);
  console.log(context);
  const connection: LoreConnection = {
    id: crypto.randomUUID(),
    value: "created-from",
    associationIds: [entity.id],
    history: [],
    type: "lore",
  };
  console.log({ relation: connection });
  return openGenerateGroupPopup({
    type,
    connections: [connection],
    context,
  });
};

export const generateEntityFrom = (entity: Entity, type: EntityType) => {
  const context = entityToContext(entity);
  // console.log(context);
  const connection: LoreConnection = {
    id: crypto.randomUUID(),
    value: "created-from",
    associationIds: [entity.id],
    history: [],
    type: "lore",
  };
  console.log({ relation: connection });
  return openExpandEntityPopup({ type, connections: [connection], context });
  // openGenerateGroupPopup({
  //   type,
  //   connections: [connection],
  //   context,
  // });
};

