import { Border, ContentEditable, Grid } from "@mjtdev/engine";
import { ReactNode, useState } from "react";

export const SpecialOptionButton = ({
  onValue,
  title,
}: {
  title?: ReactNode;
  onValue: (value: string) => void;
}) => {
  const [v, setV] = useState("");
  return (
    <Border title={title}>
      <Grid direction="column" cellSize={"min-content"}>
        <ContentEditable
          autoFocus
          style={{ minWidth: "40em" }}
          onKeyDown={(evt) => {
            if (evt.key === "Enter") {
              onValue(v);
            }
          }}
          onChange={(value) => {
            setV(value);
          }}
        />
      </Grid>
    </Border>
  );
};
