import { TextGens } from "@mjtdev/engine";
import { useEffect, useState } from "react";

export const ModelInfo = () => {
  const [info, setInfo] = useState("...");
  useEffect(() => {
    TextGens.modelInfo().then((modelInfo) => {
      setInfo(
        `${modelInfo.model_name} / ${modelInfo["shared.args"].gpu_memory.join(
          ","
        )}`
      );
    });
  }, []);

  return <div>Textgen Model: {info}</div>;
};
