import { Objects } from "@mjtdev/engine";

export const resolvePromiseMap = async <K extends string | number | symbol, V>(
  promises: Record<K, Promise<V>>
): Promise<Record<K, V>> => {
  const entries = Objects.entries(promises);
  const resolvedEntries = await Promise.all(
    entries.map(async (entry) => {
      const [key, value] = entry;
      const resolvedValue = await value;
      return [key, resolvedValue] as const;
    })
  );
  return Objects.fromEntries(resolvedEntries);
};
