import { QueryHistory } from "../../ai/QueryForDataParams";


export const uniqueHistory = (acc: QueryHistory, history: QueryHistory) => {
  const accStrings = acc.map((i) => JSON.stringify(i));
  const newHistory = history.filter((entry) => {
    const entryString = JSON.stringify(entry);
    return !accStrings.includes(entryString);
  });
  return [...acc, ...newHistory];
};
