import { create } from "zustand";
import { MainGameWindow } from "./MainGameWindow";
import { WELCOME_TEXT } from "./WELCOM_TEXT";
import { HINTS_TEXT } from "./HINTS_TEXT";
import { TextScreen } from "./TextScreen";
import { openStartNewGamePopup } from "./openStartNewGamePopup";
import { openAppSettingsPopup } from "./openAppSettingsPopup";
import { importGameUrl } from "../game/importGameUrl";

export const WINDOWS = {
  main: <MainGameWindow />,
  hints: <TextScreen text={HINTS_TEXT} />,
  // editor: <MainEditorWindow />,
  welcome: (
    <TextScreen
      text={WELCOME_TEXT}
      actions={{
        start: async () => {
          switchWindow("main");
          await openStartNewGamePopup();
        },
        "app-setup": () => {
          openAppSettingsPopup();
        },
        hints: () => {
          switchWindow("hints");
        },
        demo: () => {
          switchWindow("main");
          importGameUrl();
        },
      }}
    />
  ),
} as const;

export const useMainViewportState = create(() => ({
  content: WINDOWS["welcome"],
}));

export const switchWindow = (name: keyof typeof WINDOWS) => {
  useMainViewportState.setState((state) => ({
    ...state,
    content: WINDOWS[name],
  }));
};
