import { Bytes } from "@mjtdev/engine";
import { GameState, deleteGame, loadGame } from "./useGameState";
import { saveApp, updateAppState } from "../ui/useAppState";


export const importGameFromJsonString = async (
  rawJsonString: string,
  name: string = "Import"
) => {
  const reviver = (key: string, value: unknown) => {
    if (typeof value === "string" &&
      value.startsWith("data:image/png;base64,")) {
      return Bytes.dataUrlToBlob(value);
    }
    return value;
  };
  const json = JSON.parse(rawJsonString, reviver) as GameState;
  await deleteGame(name);
  await loadGame(name, json);
  updateAppState({
    currentGame: name,
  });
  saveApp();
};
