import { Randoms, isDefined, isUndefined } from "@mjtdev/engine";
import { parseTextToList } from "../../common/parseTextToList";
import { generateEntities } from "../generateEntities";
import { generateEntity } from "../generateEntity";
import { updateEntityImages } from "../updateEntityImages";
import { updateGameState } from "../useGameState";
import { Connection } from "./Connection";
import { EntityType } from "./EntityType";
import { Group } from "./Tribe";
import { queryEntity } from "./queryEntity";

export const generateGroup = async (
  params: { type: EntityType } & Partial<{
    connections: Connection[];
    context: string;
    prototype: Partial<Group>;
    count: number | string;
  }>
): Promise<Group> => {
  const { type, connections, context, prototype, count = 1 } = params;

  const { name, description, kind = `${type}s` } = prototype;
  if (isUndefined(name)) {
    const descPrompt = isDefined(description)
      ? ` who are described as ${description}`
      : "";
    const names = await queryEntity({
      context,
      entity: prototype,
      prompt: `create a list of 5 possible group names for a group of ${kind}${descPrompt}. NAMES ONLY!`,
    });
    const namesList = parseTextToList(names);
    console.log({ namesList });
    prototype["name"] = Randoms.pickRandom(namesList);
  }

  const group: Group = await generateEntity({
    type: "group",
    connections,
    ...prototype,
  });
  console.log({ group });
  updateGameState((state) => {
    state.entities = [...state.entities, group];
    return state;
  });
  const entities = await generateEntities({
    type,
    connections,
    context,
    parent: group,
    hint: kind,
    count: Number(count),
  });
  console.log({ entities });
  updateGameState((state) => {
    state.entities = [...state.entities, ...entities];
    return state;
  });
  // await updateEntityImages();
  return group;
};
