import { isDefined } from "@mjtdev/engine";
import { getLastElements } from "../../common/getLastElements";
import { findPerson } from "../../ui/findPerson";
import { Place } from "../Place";
import { makeScene } from "../play/makeScene";
import { getGameState, updateGameState } from "../useGameState";
import { openGameScene } from "./openGameScene";
import { updateMemories } from "./updateMemories";
import { QueryHistory } from "../../ai/QueryForDataParams";

export const showScene = async (params: {
  personIds: string[];
  instruction: string;
  context?: string;
  title?: string;
  history?: QueryHistory;
  place: Place;
}) => {
  const { context: superContext, persona } = getGameState();
  const { title, history, place: location, personIds, instruction, context } = params;
  const persons = personIds.map((id) => findPerson(id));

  const scene = await makeScene({
    persons,
    title,
    instruction,
    location,
    history,
    context: [superContext, context].filter(isDefined).join("\n"),
    persona,
  });
  const pickPromise = openGameScene(scene);
  for (let person of persons) {
    await updateMemories(person, scene);
  }
  const pick = await pickPromise;
  updateGameState((state) => {
    state.scenes = [...state.scenes, scene];
    return state;
  });
  return { pick, scene };
};
