import { Idb, Idbs, createState } from "@mjtdev/engine";
import { INITIAL_GAME_STATE } from "./INITIAL_GAME_STATE";
import { getAppState } from "../ui/useAppState";

export const [useGameState, updateGameState] = createState(INITIAL_GAME_STATE);

export const getGameState = () => useGameState.getState();

export type GameState = ReturnType<(typeof useGameState)["getState"]>;

export const GAME_DB: Idb<GameState> = {
  dbName: "rpg-engine",
  storeName: "games",
};

export const saveGame = (key: string = getAppState().currentGame) => {
  const state = getGameState();
  console.log({ key });
  return Idbs.put(GAME_DB, key, state);
};

export const listGames = async () => {
  const keys = await Idbs.list(GAME_DB);
  return keys.map((k) => String(k));
};

export const deleteGame = (key: string) => {
  return Idbs.remove(GAME_DB, key);
};
export const loadGame = async (
  key = "current",
  initial = INITIAL_GAME_STATE
) => {
  const state = await Idbs.get(GAME_DB, key, () => initial);
  updateGameState(state);
  return state;
};
