import { Border, Grid } from "@mjtdev/engine";
import { ReactNode, useState } from "react";
import { TribesDisplay } from "./TribesDisplay";
import { PlacesDisplay } from "./PlacesDisplay";
import { ThingsDisplay } from "./ThingsDisplay";
import { LoresDisplay } from "./LoresDisplay";
import { GameSceneLog } from "./GameSceneLog";
import { EntitiesDisplay } from "./EntitiesDisplay";

export const CenterGameDisplay = ({
  style,
}: {
  style?: React.CSSProperties;
}) => {
  const contentMap = {
    Lore: (
      <LoresDisplay
        childrenStyle={{
          maxHeight: "10em",
          overflow: "auto",
          minWidth: "50em",
          maxWidth: "80em",
        }}
      />
    ),
    // People: <TribesDisplay />,
    People: <EntitiesDisplay type="person" />,
    Places: <EntitiesDisplay type="place" />,
    Things: <EntitiesDisplay type="thing" />,
    Scenes: <GameSceneLog />,
  };

  const [content, setContent] = useState<ReactNode>(contentMap["Lore"]);
  const [selected, setSelected] = useState<keyof typeof contentMap>("Lore");

  const buttons = Object.entries(contentMap).map((entry, i) => {
    const [name, display] = entry;
    return (
      <button
        disabled={selected === name}
        key={i}
        onClick={() => {
          setContent(display);
          setSelected(name as keyof typeof contentMap);
        }}
      >
        {name}
      </button>
    );
  });
  const buttonDisplay = (
    <Grid direction="column" cellSize={"min-content"}>
      {buttons}
    </Grid>
  );
  return (
    <Border title={buttonDisplay}>
      <div style={style}>{content}</div>
    </Border>
  );
};
