import { AiLogEntry } from "./AiLogEntry";
import { updateAiLogState } from "./updateAiLogState";

export const updateAiLogEntry = (entry: Partial<AiLogEntry>) => {
  updateAiLogState((state) => {
    const e = state.log.find((e) => e.id === entry.id);
    const {
      id,
      prompt = entry.prompt,
      promptTokenCount = entry.promptTokenCount,
      response = entry.response,
    } = e;
    e.prompt = prompt;
    e.promptTokenCount = promptTokenCount;
    e.response = response;
    return state;
  });
};
