import { updateGameState } from "./useGameState";

export const advanceTime = (amountHrs = 2) => {
  updateGameState((state) => {
    const { time } = state;
    const d = new Date(time);
    const hours = d.getHours();
    d.setHours(hours + amountHrs);
    state.time = d.toISOString();
    return state;
  });
};
