import { Border, Grid, Objects, isDefined } from "@mjtdev/engine";
import { Connection } from "../game/play/Connection";
import { idToImage } from "../game/play/idToImage";
import { nameOf } from "../game/play/nameOf";
import { blobToUrl } from "./blobToUrl";
import { updateGameState } from "../game/useGameState";

export const ConnectionDisplay = ({ relation }: { relation: Connection }) => {
  const { type, value, associationIds, history, id } = relation;
  const images = associationIds.map((id, i) => {
    const blob = idToImage(id);
    const url = blobToUrl(id, blob);
    return (
      <img
        onClick={() => {
          updateGameState({ selectedId: id });
        }}
        title={nameOf(id)}
        style={{ height: "2em" }}
        key={i}
        src={url}
      />
    );
  });

  return (
    <Grid
      cellSize={[...Objects.times(images.length, () => "min-content"), "1fr"]}
      direction="column"
    >
      {images}
      <Border
        title={<div style={{ whiteSpace: "nowrap", padding: 0 }}>{type}</div>}
      >
        {value}
      </Border>
    </Grid>
  );
};
