import { isDefined } from "@mjtdev/engine";
import { queryForData } from "../../ai/queryForData";
import { isNotEmpty } from "../../common/isNotEmpty";
import { findEntity } from "../../ui/findEntity";
import { getGameState } from "../useGameState";
import { Entity } from "./Entity";
import { entityToContext } from "./entityToContext";
import { getEntityPreStyle } from "./getEntityPreStyle";
import { getPromptStyleForEntityType } from "./getPromptStyleForEntityType";

export const generateEntityDescription = async (
  entity: Partial<Entity>,
  options: Partial<{ context: string }> = {}
) => {
  const { context, persona } = getGameState();
  const parentContext = entityToContext(findEntity(entity.parent));

  const { name, parent, type } = entity;
  const promptStyle = getPromptStyleForEntityType(type);

  const preStyle = getEntityPreStyle();
  const {
    prompt = [
      preStyle,
      `'${name}'`,
      `${
        isDefined(parent)
          ? `which is a part of '${findEntity(parent).name}'`
          : ""
      }.`,
      promptStyle,
    ]
      .filter(isNotEmpty)
      .join(" "),
  } = entity;
  const { context: optionsContext } = options;
  const resp = await queryForData({
    prompt,
    context: [context, parentContext, optionsContext]
      .filter(isDefined)
      .join("\n"),
    persona,
    filter: (r) => isNotEmpty(r?.answer),
    attempts: 3,
    legend: { answer: "the answer" },
    textGenParams: { max_new_tokens: 1024 },
  });
  return {
    prompt,
    description: resp.answer,
  };
};
