import { Htmls, Images, isDefined } from "@mjtdev/engine";
import { drawImagePrompt } from "../ai/drawImagePrompt";
import { IMAGE_URL_CACHE } from "../ui/IMAGE_URL_CACHE";
import { Entity } from "./play/Entity";
import { idToName } from "./play/idToName";
import { queryLooksLike } from "./queryLooksLike";
import { getGameState } from "./useGameState";

export const drawEntityImage = async (
  entity: Entity,
  options: Partial<{
    postStyle: string;
    preStyle: string;
    midStyle: string;
  }> = {}
) => {
  const {
    preStyle = getGameState().imagePreStyle,
    midStyle = entity.type === "person"
      ? "headshot"
      : "(no people), (no person in view)",
    postStyle = getGameState().imagePostStyle,
  } = options;

  const looksLike = await queryLooksLike(entity);

  const prompt = [preStyle, idToName(entity.id), midStyle, looksLike, postStyle]
    .filter(isDefined)
    .join(" ");
  const canvas = Htmls.from("canvas", {
    attributes: { width: 512, height: 512 },
  });
  await drawImagePrompt({
    prompt,
    canvas,
    steps: 20,
    seed: Date.now(),
  });
  const image = await Images.toBlob(canvas);
  IMAGE_URL_CACHE.clear();
  return image;
};
