import { Grid } from "@mjtdev/engine";
import { Entity } from "../game/play/Entity";
import { useGameState } from "../game/useGameState";
import { EntityDisplay } from "./EntityDisplay";

export const ExpandedEntityDisplay = ({
  entity,
  options,
}: {
  options?: Partial<{
    imageStyle?: React.CSSProperties;
    textStyle?: React.CSSProperties;
    style?: React.CSSProperties;
    showPrompt: boolean;
    showDescription: boolean;
    showValues: boolean;
  }>;

  entity: Entity;
}) => {
  const { entities } = useGameState();
  const children = entities.filter((t) => t.parent === entity.id);
  const contents = children.map((child, i) => (
    <EntityDisplay options={options} key={i} entity={child} />
  ));

  return (
    <Grid direction="column" count={1} cellSize={"min-content"}>
      {contents}
    </Grid>
  );
};
