import { isDefined } from "@mjtdev/engine";
import { Lore } from "./Lore";
import { generateEntityDescription } from "./play/generateEntityDescription";
import { nameOf } from "./play/nameOf";
import { getFlakeDescriptionContext } from "./getFlakeDescriptionContext";

export const generateLore = async (
  prototype: Partial<Lore> = {}
): Promise<Lore> => {
  const {
    id = crypto.randomUUID(),
    parent,
    name,
    values = {},
    connections = [],
    kind = "description",

    prompt = `create a brief, terse, ${kind} of '${nameOf(prototype)}'${
      isDefined(parent) ? ` within '${nameOf(parent)}'` : ""
    } for the story.`,

    description: undefined,
  } = prototype;

  const context = getFlakeDescriptionContext(prototype);
  const { prompt: updatedPrompt, description } =
    await generateEntityDescription({ ...prototype, prompt }, { context });

  return {
    connections,
    type: "lore",
    id,
    kind: kind,
    description,
    parent,
    name,
    values,
    prompt: updatedPrompt,
  };
};
