import { Border, Grid, isUndefined } from "@mjtdev/engine";
import { EntityDisplay } from "./EntityDisplay";
import { openNewLorePopup } from "./openNewLorePopup";
import { useEntitiesByType } from "./useEntitiesByType";

export const LoresDisplay = ({
  style = {},
  childrenStyle = {},
}: {
  style?: React.CSSProperties;
  childrenStyle?: React.CSSProperties;
}) => {
  const lores = useEntitiesByType("lore");

  const contents = lores
    .filter((t) => isUndefined(t.parent))
    .map((flake, i) => (
      <EntityDisplay
        key={i}
        entity={flake}
        options={{
          imageStyle: { maxHeight: "2em" },
          textStyle: {
            maxHeight: "10em",
            overflow: "auto",
            ...childrenStyle,
          },
          showDescription: true,
          showImage: false,
        }}
      />
    ));
  return (
    <Border
      style={{ maxHeight: "80vh", overflow: "auto", ...style }}
      title="lore"
    >
      <button
        onClick={() => {
          openNewLorePopup(undefined);
        }}
      >
        Add Lore
      </button>
      <Grid direction="column" cellSize={"min-content"} count={1}>
        {contents}
      </Grid>
    </Border>
  );
};
