import { isDefined } from "@mjtdev/engine";
import { useGameState } from "../game/useGameState";
import { EntityDisplay } from "./EntityDisplay";

export const SelectedIdDisplay = ({
  style,
  imageStyle,
}: {
  style?: React.CSSProperties;
  imageStyle?: React.CSSProperties;
}) => {
  const { entities, selectedId } = useGameState();
  const entity = entities.find((e) => e.id === selectedId);
  if (isDefined(entity)) {
    return (
      <EntityDisplay
        options={{
          imageStyle,
          style: { maxWidth: "20vw", maxHeight: "80vh", overflow: "auto" },
          textStyle: { maxHeight: "20em", minWidth: "16vw", maxWidth: "16vw" },
          showPrompt: true,
          showValues: true,
          showDescription: true,
          showGoals: true,
          showMemories: true,
        }}
        entity={entity}
      />
    );
  }

  return <div></div>;
};
