import { Objects } from "@mjtdev/engine";
import { QueryForDataParams } from "./QueryForDataParams";
import { defaultedParams } from "./defaultedParams";


export const recordToQueryResponseText = (
  record: Record<string, string>,
  params: Partial<QueryForDataParams>
) => {
  const { propertyPrefix, start, end } = defaultedParams(params);
  const entries = Objects.entries(record).map(
    (e) => `${propertyPrefix}${e[0]}: ${e[1]}`
  );

  return [start, ...entries, end].join("\n");
};
