import { isUndefined } from "@mjtdev/engine";
import { findEntities } from "./findEntities";
import { updateEntityImage } from "./updateEntityImage";

export const updateEntityImages = async () => {
  const entities = findEntities();
  for (let entity of entities) {
    if (isUndefined(entity.image)) {
      await updateEntityImage(entity);
    }
  }
};
