import { isDefined, isUndefined } from "@mjtdev/engine";
import { queryForData } from "../ai/queryForData";
import { parseTextToList } from "../common/parseTextToList";
import { Connection } from "./play/Connection";
import { Entity } from "./play/Entity";
import { entityToContext } from "./play/entityToContext";
import { getGameState } from "./useGameState";
import { EntityType } from "./play/EntityType";
import { isNotEmpty } from "../common/isNotEmpty";
import { generateEntity } from "./generateEntity";
import { TypedEntity } from "../ui/TypedEntity";
import { csvTextToRecords } from "./csvTextToRecords";

export const generateEntities = async <T extends EntityType>(
  params: Partial<{
    context: string;
    parent: Entity;
    type: T;
    hint: string;
    connections: Connection[];
    count: number;
    legend?: Record<string, string>;
  }> = {}
): Promise<TypedEntity<T>[]> => {
  const { context, persona } = getGameState();
  const {
    context: superContext,
    parent,
    count,
    type = parent.type,
    hint = type,
    legend = {},
  } = params;

  const expandedLegend = { name: `${type} name`, ...legend };

  const recordKeyCount = Object.keys(expandedLegend).length;

  const parentContext = entityToContext(parent);
  const withinPrompt = isDefined(parent) ? ` within ${parent.name}` : "";
  const recordKeysPrompt = Object.keys(expandedLegend)
    .map((x) => x.toLocaleUpperCase())
    .join(",");
  const listPrompt = `Be terse. Create a numbered bullet point list of`;
  const formatInstruction = "Be sure to follow the Record Format exactly.";
  const namesPrompt = `${listPrompt} ${count} ${hint} ${type}s ${recordKeysPrompt}${withinPrompt} for the story. ${formatInstruction}`;

  const csvAnswer = `CSV list of ${recordKeysPrompt}`;

  const recordContext = `Record Format for answer:\n${recordKeysPrompt}\n`;
  const namesResp = await queryForData({
    prompt: namesPrompt,
    context: [context, superContext, parentContext, recordContext]
      .filter(isDefined)
      .join("\n"),
    persona,
    attempts: 5,
    legend: { answer: csvAnswer },
    filter: (resp) => {
      const records = parseTextToList(resp?.answer)
        .map((line) => {
          if (/NAME/i.test(line)) {
            return undefined;
          }
          const values = line
            .split(",")
            .map((x) => x.trim())
            .filter(isNotEmpty);
          if (values.length !== recordKeyCount) {
            // console.log({ values, line });
            // console.log(`wrong number of values ${values.length}`);
            return undefined;
          }
          return true;
        })
        .filter(isDefined);
      return records.length >= count;
    },
  });
  if (isUndefined(namesResp)) {
    return undefined;
  }
  const entityRecords = csvTextToRecords(
    namesResp.answer,
    recordKeysPrompt.split(",").map((x) => x.toLocaleLowerCase())
  );
  console.log({ entityRecords });

  const entities = (await Promise.all(
    entityRecords.map(({ name, ...values }) =>
      generateEntity({ parent: parent?.id, name, values, type })
    )
  )) as TypedEntity<T>[];
  return entities;
};
