import { Border, Grid } from "@mjtdev/engine";
import { updateGameState, useGameState } from "../game/useGameState";
import { useEffect, useRef } from "react";
import { GameSceneLogEntry } from "./GameSceneLogEntry";
import { ButtonGroup } from "./ButtonGroup";
import { startRandomInteraction } from "../game/interaction/startRandomInteraction";
import { getPlayer } from "./getPlayer";

export const GameSceneLog = () => {
  const { scenes } = useGameState();

  const bottomRef = useRef<HTMLDivElement>();

  useEffect(() => {
    if (bottomRef.current) {
      bottomRef.current.scrollIntoView({
        behavior: "instant",
      });
    }
  }, [scenes]);

  const content = scenes.map((scene, i) => {
    return <GameSceneLogEntry scene={scene} key={i} />;
  });
  return (
    <Border title="scenes">
      <Grid cellSize={"min-content"} gap={"1em"} direction="row">
        <ButtonGroup
          actions={{
            clear: () => {
              updateGameState({ scenes: [] });
            },
            "Next Random Interaction": () => startRandomInteraction(),
            "Next Player Interaction": () =>
              startRandomInteraction(getPlayer()),
          }}
        />
        <Grid
          style={{ overflow: "auto", maxHeight: "80vh" }}
          direction="row"
          cellSize={"min-content"}
        >
          {content}
          <div ref={bottomRef}></div>
        </Grid>
      </Grid>
    </Border>
  );
};
