import { startInteraction } from "./startInteraction";
import { getInteractionsList } from "./getInteractionsList";
import { findPerson } from "../../ui/findPerson";
import { findPlace } from "../../ui/findPlace";
import { getPlayer } from "../../ui/getPlayer";

export const getInteractions = async (personId: string) => {
  const list = await getInteractionsList(personId);
  const entries = list.map(
    (instruction) =>
      [
        instruction,
        async () => {
          const person = findPerson(personId);
          const place = findPlace(person.values.location);
          const player = getPlayer();
          return startInteraction({
            instruction,
            personIds: [player.id, personId],
            place,
          });
        },
      ] as const
  );
  return Object.fromEntries(entries);
};
