import { updateGameState } from "./useGameState";
import { Entity } from "./play/Entity";
import { queryEntity } from "./play/queryEntity";
import { nameOf } from "./play/nameOf";
import { isDefined } from "@mjtdev/engine";
import { findEntity } from "../ui/findEntity";
import { entityToContext } from "./play/entityToContext";

export const queryLooksLike = async (entity: Entity) => {
  const { values = {} } = entity;
  const key = "looksLike";
  const parent = findEntity(entity.parent);
  const context = entityToContext(entity);
  const looksLike =
    values[key] ||
    (await queryEntity({
      entity,
      context,
      prompt: `Be brief. Be terse. Describe only the physical features of '${nameOf(entity)}' the ${
        entity.type
      }${
        isDefined(parent) ? ` which is a part of '${nameOf(parent)}'` : ""
      } as a camera would see. Include description of the overall the environment it is a part of as well.`,
    }));

  updateGameState((state) => {
    const { entities } = state;
    const e = entities.find((e) => e.id === entity.id);
    e.values[key] = looksLike;
    return state;
  });

  return looksLike;
};
