import { Border, Center, Grid, isUndefined } from "@mjtdev/engine";
import { formatText } from "../../common/formatText";
import { isNotEmpty } from "../../common/isNotEmpty";
import { PersonDisplay } from "../../ui/PersonDisplay";
import { blobToUrl } from "../../ui/blobToUrl";
import { openEntityContextMenu } from "../../ui/openEntityContextMenu";
import { GameScene } from "./GameScene";
import { SpecialOptionButton } from "./SpecialOptionButton";
import { nameOf } from "../play/nameOf";
import { startRandomInteraction } from "./startRandomInteraction";
import { ButtonGroup } from "../../ui/ButtonGroup";

export const GameScenePopup = ({
  scene,
  onPick,
}: {
  onPick: (pick: string) => void;
  scene: GameScene;
}) => {
  const { title, place, text, background, persons, options } = scene;

  const optionButtons = options.map((o, i) => (
    <button
      style={{ width: "min-content" }}
      key={i}
      onClick={() => {
        onPick(o);
      }}
    >
      {o}
    </button>
  ));

  const avatars = persons.map((c, i) => (
    <PersonDisplay
      key={i}
      imageStyle={{ height: "8em" }}
      style={{ maxWidth: "10em", height: "10em" }}
      person={c}
    />
  ));

  const locationImageUrl = blobToUrl(place.id, place.image);

  return (
    <div
      style={{
        maxWidth: "80vw",
        maxHeight: "80vh",
        overflow: "auto",
        backgroundColor: "black",
      }}
    >
      <Border
        title={
          <div
            style={{
              maxWidth: "30em",
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
            }}
          >
            {[title].filter(isNotEmpty).join(", ")}
          </div>
        }
      >
        <Grid
          style={{ backgroundColor: "black", border: "1px solid white" }}
          direction="row"
          gap={"0.5em"}
          cellSize={"min-content"}
        >
          <Center>
            <Grid direction="column" cellSize={"min-content"}>
              <Border
                style={{ padding: 0, borderColor: "black" }}
                title={
                  <div
                    style={{
                      maxWidth: "16em",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                    }}
                  >
                    {place.name}
                  </div>
                }
              >
                <img
                  style={{ maxHeight: "40vh" }}
                  onContextMenu={(evt) => openEntityContextMenu(evt, place)}
                  src={locationImageUrl}
                />
              </Border>
              <Grid direction="row" cellSize={"min-content"}>
                {avatars}
              </Grid>
            </Grid>
          </Center>
          <Center>
            <div
              style={{
                maxHeight: "10vh",
                overflow: "auto",
                // minWidth: "50em",
                maxWidth: "70vw",
                padding: "1em",
                whiteSpace: "pre",
              }}
            >
              {formatText(text)}
            </div>
          </Center>
          <Grid gap={"1em"} direction="row">
            <Grid gap={"1em"} direction="row" cellSize={"min-content"}>
              <SpecialOptionButton
                title={
                  <div
                    style={{
                      maxWidth: "16em",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                    }}
                  >
                    {nameOf(persons[0])} chose to...
                  </div>
                }
                onValue={onPick}
              />
              {optionButtons}
              <Center style={{ paddingBottom: "1em" }}>
                <ButtonGroup
                  actions={{
                    "continue scene": () => onPick("continue the scene"),
                    [`next scene with ${persons[0].name}`]: () => {
                      onPick(undefined);
                      startRandomInteraction(persons[0]);
                    },
                    "next scene with random person": () => {
                      onPick(undefined);
                      startRandomInteraction(undefined);
                    },
                    close: () => onPick(undefined),
                  }}
                />
              </Center>
            </Grid>
          </Grid>
        </Grid>
      </Border>
    </div>
  );
};
