import { Center, Grid, Objects, centerPopup, closePopup } from "@mjtdev/engine";
import { PopupForm } from "./PopupForm";
import { useEffect, useRef } from "react";

export const PopupFormDisplay = <
  K extends string | number | symbol = string,
  V extends string | boolean = string
>({
  title,
  name,
  fieldComponents,
  // resolve,
  // result,
  onSubmit = () => {},
}: {
  // result: PopupForm<K, V>;
  onSubmit?: (ok: boolean) => void;
  // resolve: (value: Record<K, V> | PromiseLike<Record<K, V>>) => void;
  name: string;
  title: string;
  fieldComponents: JSX.Element[];
}) => {
  const ref = useRef<HTMLDivElement>();
  useEffect(() => {
    centerPopup(name, ref.current);
  }, [ref]);

  return (
    <div
      ref={ref}
      style={{
        padding: "1em",
        backgroundColor: "black",
        border: "1px solid white",
        maxHeight: "80vh",
        overflow: "auto",
      }}
    >
      <Grid direction="row" cellSize={"min-content"} gap="1em">
        <Center>{title}</Center>
        <form
          onKeyDown={(evt) => {
            if (evt.ctrlKey && evt.key.toLocaleLowerCase() === "enter") {
              evt.preventDefault();
              closePopup(name);
              onSubmit(true);
              // resolve(
              //   Objects.fromEntries(
              //     result.fields.map(({ name, value }) => [name, value] as const)
              //   )
              // );
            }
            if (evt.key.toLocaleLowerCase() === "escape") {
              closePopup(name);
              // resolve(undefined);
              onSubmit(false);
            }
          }}
          onSubmit={(evt) => {
            evt.preventDefault();
            closePopup(name);
            onSubmit(true);
            // resolve(
            //   Objects.fromEntries(
            //     result.fields.map(({ name, value }) => [name, value] as const)
            //   )
            // );
          }}
        >
          <Grid direction="row" cellSize={"min-content"} gap="0.1em">
            {fieldComponents}
          </Grid>
          <Center style={{ padding: "1em" }}>
            <Grid gap={"1em"} cellSize={"min-content"}>
              <input type="submit" value="OK" />
              <input
                type="button"
                value="Cancel"
                onClick={() => {
                  closePopup(name);
                  onSubmit(false)
                  // resolve(undefined);
                }}
              />
            </Grid>
          </Center>
        </form>
      </Grid>
    </div>
  );
};
