import { Border, Grid, openContextMenu } from "@mjtdev/engine";
import { updatePerson } from "../game/interaction/updatePerson";
import { Person } from "../game/play/Person";
import { ConnectionDisplay } from "./ConnectionDisplay";

export const ConnectionsDisplay = ({
  style = {},
  person,
}: {
  style?: React.CSSProperties;
  person: Person;
}) => {
  const relations = person.connections.map((r, i) => (
    <div
      onContextMenu={(evt) =>
        openContextMenu(evt, {
          delete: () => {
            updatePerson({
              ...person,
              connections: person.connections.filter((pm) => pm.id !== r.id),
            });
          },
        })
      }
      key={i}
    >
      <ConnectionDisplay relation={r} />
    </div>
  ));

  return (
    <Border style={{ overflow: "auto" }} title="connections">
      <Grid
        direction="row"
        style={style}
        // style={{ maxHeight: "5em", overflow: "auto" }}
        cellSize={"min-content"}
      >
        {relations}
      </Grid>
    </Border>
  );
};
