export const mlOutputParser = (text: string, propertyPrefix: string) => {
  const prefixBegin = propertyPrefix[0];
  const prefixCount = propertyPrefix.length;
  // const REGEX = /^###([a-zA-Z0-9]+):([^#]*)/gims;
  const regex = new RegExp(
    `^${propertyPrefix}([a-zA-Z0-9]+):([^${prefixBegin}]*)`,
    "gims"
  );
  const fullTokenRegex = new RegExp(`${prefixBegin}{${prefixCount}}`, "g");
  const regexBegin = new RegExp(`${prefixBegin}`, "g");

  const fullDummyToken = "1XXXXXXXXX1";
  const beginDummyToken = "2YYYYYYYYY2";

  const cleanText = text
    // .replaceAll(/#{3}/g, "<3-HASH>")
    // .replaceAll(/#/g, "<HASH>")
    .replaceAll(fullTokenRegex, fullDummyToken)
    .replaceAll(regexBegin, beginDummyToken)
    .replaceAll(fullDummyToken, propertyPrefix);
  const matches = Array.from(cleanText.matchAll(regex));

  const entries = matches.map((match) => {
    const [_, key, value] = match;
    // console.log({ key, value, _ });
    // return [key.trim(), value.trim().replaceAll("<HASH>", "#")] as const;
    return [
      key.trim(),
      value.trim().replaceAll(beginDummyToken, prefixBegin),
    ] as const;
  });
  // console.log({ entries, matches, cleanText });
  return Object.fromEntries(entries);
};
