import { findEntity } from "../ui/findEntity";
import { drawEntityImage } from "./drawEntityImage";
import { Entity } from "./play/Entity";
import { updateGameState } from "./useGameState";

export const updateEntityImage = async (
  entity: Entity,
  options: Partial<{
    clearLooksLike: boolean;
  }> = {}
) => {
  const { clearLooksLike = false } = options;
  if (clearLooksLike) {
    updateGameState((state) => {
      const { entities } = state;
      const t = entities.find((t) => t.id === entity.id);
      console.log({ t });
      delete t.values["looksLike"];
      console.log({ t });
      return state;
    });
  }

  const updatedEntity = findEntity(entity.id);

  const image = await drawEntityImage(updatedEntity);

  updateGameState((state) => {
    const { entities } = state;
    const t = entities.find((t) => t.id === entity.id);
    t.image = image;
    return state;
  });
};
