import { isEmpty } from "../../common/isEmpty";
import { Person } from "../play/Person";
import { Memory, Connection } from "../play/Connection";
import { queryMemory } from "../play/queryMemory";
import { updateGameState } from "../useGameState";
import { GameScene } from "./GameScene";

export const updateMemories = async (person: Person, scene: GameScene) => {
  const ids = [
    ...scene.persons.map((p) => p.id).filter((id) => id !== person.id),
    scene.place.id,
  ];
  const memory = await queryMemory(person.id, {
    associationIds: ids,
    history: [scene.lastHistoryEntry],
  });
  updateGameState((state) => {
    const p = state.entities.find((e) => e.id === person.id) as Person;
    p.connections = addMemories(p.connections, memory);
    return state;
  });
};

export const addMemories = (acc: Connection[], memory: Memory) => {
  if (isEmpty(memory.value)) {
    return acc;
  }
  return [...acc, memory];
};
