import { QueryHistory } from "../../ai/QueryForDataParams";
import { queryForData } from "../../ai/queryForData";
import { getLastElements } from "../../common/getLastElements";
import { findPerson } from "../../ui/findPerson";
import { getPlayer } from "../../ui/getPlayer";
import { getGameContext } from "../interaction/getGameContext";
import { getGameState } from "../useGameState";
import { Memory } from "./Connection";
import { entityToContext } from "./entityToContext";
import { nameOfPerson } from "./nameOfPerson";

export const queryMemory = async (
  personId: string,
  options: Partial<{ associationIds: string[]; history: QueryHistory }> = {}
): Promise<Memory> => {
  const { history = [], associationIds = [] } = options;
  const { persona } = getGameState();
  // const player = getPlayer();
  const person = findPerson(personId);
  const gameContext = getGameContext();
  // const playerContext = entityToContext(player, {
  //   associationIds,
  // });
  const personContext = entityToContext(person, {
    includeMemory: false,
    includeLore: false,
    associationIds,
  });
  const context = [gameContext, personContext].join("\n");
  // const prompt = `What will ${nameOfPerson(
  //   person
  // )} remember ${nameOfPerson(player)}`;
  const prompt = `Be brief. Be terse. Use proper names. DO NOT USE PRONOUNS! In a single sentence what will ${nameOfPerson(
    person
  )} be able remember about the last scene if anything?`;

  // const memoryHistory = Randoms.pickRandoms({
  //   from: person.memories.filter(
  //     (m) => intersect(m.associationIds, associationIds).length > 0
  //   ),
  //   count: 5,
  //   forceUnique: true,
  // })
  //   .filter(isDefined)
  //   .map((m) => m.history)
  //   .flat();

  const resp = await queryForData({
    prompt,
    context,
    persona,
    // history: getLastElements([...person.history, ...history], 3),
    // history: getLastElements([...memoryHistory, ...history], 3),
    history: getLastElements([...history], 1),
    legend: {
      answer: "The memory",
    },
    textGenParams: {
      repetition_penalty: 1.3,
      max_new_tokens: 128,
      temperature: 0.5,
    },
  });
  console.log(resp);

  return {
    type: "memory",
    id: crypto.randomUUID(),
    history,
    associationIds: associationIds,
    value: resp?.answer,
  };
  // return resp.answer;
};
