import { isDefined, isUndefined } from "@mjtdev/engine";
import { Lore } from "../game/Lore";
import { generateLores } from "../game/generateLores";
import { updateGameState } from "../game/useGameState";
import { openFormPopup } from "./form/openFormPopup";

export const openExpandLorePopup = async (parent: Lore) => {
  const resp = await openFormPopup({
    title: "Expand Flake",
    fields: [
      {
        name: "namesPrompt",
        value: isDefined(parent) ? "lore section names" : "general names",
      },
      { name: "kind", value: "lore" },
      { name: "count", value: "3" },
    ],
  });
  if (isUndefined(resp)) {
    return;
  }

  const hint = isDefined(parent)
    ? `${resp.namesPrompt} within '${parent.name}'`
    : resp.namesPrompt;

  const lores = await generateLores({
    count: Number(resp.count),
    parent,
    hint,
    kind: resp.kind,
  });
  updateGameState((state) => {
    state.entities.push(...lores);
    return state;
  });
};
