import { Grid, openContextMenu } from "@mjtdev/engine";
import { nameOfPerson } from "../game/play/nameOfPerson";
import { Person } from "../game/play/Person";
import { AssetAvatar } from "../game/interaction/AssetAvatar";
import { getInteractions } from "../game/interaction/getInteractions";
import { updateGameState } from "../game/useGameState";

export const CompactPersonDisplay = ({ person }: { person: Person }) => {
  const avatar = <AssetAvatar style={{ height: "4em" }} asset={person} />;

  return (
    <Grid
      style={{ padding: "0.1em", border: "1px solid white" }}
      direction="column"
    >
      <div
        onClick={() => updateGameState({ selectedId: person.id })}
        onContextMenu={async (evt) => {
          evt.preventDefault();
          const interactions = await getInteractions(person.id);

          openContextMenu(evt, interactions);
        }}
      >
        {avatar}
      </div>
      <div>{nameOfPerson(person)}</div>
    </Grid>
  );
};
