import { Border, Center, ContentEditable, Grid } from "@mjtdev/engine";
import { useState } from "react";
import { isEmpty } from "../common/isEmpty";
import { nameOfPerson } from "../game/play/nameOfPerson";
import { CompactPersonDisplay } from "./CompactPersonDisplay";
import { useEntitiesByType } from "./useEntitiesByType";

export const PersonSearchPopup = () => {
  const persons = useEntitiesByType("person");

  const [filter, setFilter] = useState("");
  const filteredPersons = persons.filter((p) => {
    if (isEmpty(filter)) {
      return true;
    }
    return nameOfPerson(p).toLocaleLowerCase().includes(filter);
  });
  const personItems = filteredPersons.map((p, i) => (
    <CompactPersonDisplay key={i} person={p} />
  ));

  return (
    <Border
      title={
        <div style={{ backgroundColor: "black", whiteSpace: "nowrap" }}>
          Search People
        </div>
      }
    >
      <Grid
        style={{ backgroundColor: "black" }}
        direction="row"
        cellSize={"min-content"}
      >
        <ContentEditable
          autoFocus
          style={{ minWidth: "20em", maxWidth: "20em" }}
          onChange={(value) => {
            setFilter(value);
          }}
        />
        {personItems}
      </Grid>
    </Border>
  );
};
