import { isDefined, isUndefined } from "@mjtdev/engine";
import { isEmpty } from "../common/isEmpty";
import { Lore } from "../game/Lore";
import { generateLore } from "../game/generateLore";
import { updateGameState } from "../game/useGameState";
import { openFormPopup } from "./form/openFormPopup";
import { updateEntityImages } from "../game/updateEntityImages";

export const openNewLorePopup = async (parent: Lore) => {
  const resp = await openFormPopup({
    title: `Generate Lore${isDefined(parent) ? ` within ${parent.name}` : ""}`,
    fields: [
      {
        name: "name",
      },
      {
        name: "kind",
        value: "general",
      },
      {
        name: "prompt",
      },
      {
        name: "description",
      },
    ],
  });
  if (isUndefined(resp)) {
    return;
  }
  console.log({ resp });
  const lore: Lore = isEmpty(resp.description)
    ? await generateLore({ ...resp, parent: parent?.id })
    : {
        description: resp.description,
        id: crypto.randomUUID(),
        kind: resp.kind,
        connections: [],
        name: resp.name,
        parent: undefined,
        prompt: resp.prompt,
        type: "lore",
        values: {},
        image: undefined,
      };
  console.log({ flake: lore });

  updateGameState((state) => {
    state.entities.push(lore);
    return state;
  });
  updateEntityImages();
};
