import { AiLogEntry } from "./AiLogEntry";
import { updateAiLogState } from "./updateAiLogState";

export const addAiLogEntry = (entry: Partial<AiLogEntry>) => {
  const {
    id = crypto.randomUUID(),
    prompt,
    promptTokenCount,
    response,
    type,
  } = entry;
  updateAiLogState((state) => {
    state.log.push({ id, prompt, promptTokenCount, response, type });
    if (state.log.length > state.maxLogSize) {
      state.log = state.log.slice(1);
    }
    return state;
  });
  return id;
};
