import { isDefined } from "@mjtdev/engine";
import { queryForData } from "../ai/queryForData";
import { parseTextToList } from "../common/parseTextToList";
import { Lore } from "./Lore";
import { generateLore } from "./generateLore";
import { entityToContext } from "./play/entityToContext";
import { getGameState } from "./useGameState";
import { isNotEmpty } from "../common/isNotEmpty";

export const generateLores = async (
  options: Partial<{
    parent: Lore;
    hint: string;
    count: number;
    kind: string;
  }> = {}
): Promise<Lore[]> => {
  const { context, persona } = getGameState();
  const { parent, count, hint = "general concepts", kind } = options;

  const parentContext = entityToContext(parent);
  const prompt = `Be terse. Create a numbered bullet point list of ${count} ${hint} for the story. NAMES ONLY!`;
  const resp = await queryForData({
    prompt,
    context: [context, parentContext].filter(isDefined).join("\n"),
    persona,
    attempts: 3,
    filter: (r) => isNotEmpty(r?.answer),
    legend: { answer: "the names" },
  });
  const list = parseTextToList(resp.answer);

  return Promise.all(
    list.map((name) => generateLore({ name, parent: parent?.id, kind }))
  );
};
