import { QueryForDataParams } from "./QueryForDataParams";
import { recordToQueryResponseText } from "./recordToQueryResponseText";
import { defaultedParams } from "./defaultedParams";


export const queryHistoryToText = (params: Partial<QueryForDataParams>) => {
  const { user, assistant, history } = defaultedParams(params);
  console.log({ history });
  return (
    history
      .map((entry) => {
        const { prompt, response } = entry;
        const responseText = recordToQueryResponseText(response, params);
        return [
          [user, prompt].join(" "),
          [assistant, responseText].join(" "),
        ].join("\n");
      })
      // .flat()
      .join("\n")
  );
};
