import { Border, isDefined } from "@mjtdev/engine";
import { AiLogEntry } from "./AiLogEntry";

export const AiLogEntryDisplay = ({ entry }: { entry: AiLogEntry }) => {
  const { type, promptTokenCount, prompt, response } = entry;
  const logText = type === "image" ? prompt : response;
  const titleText =
    type === "image" ? undefined : `(${promptTokenCount}) ${prompt}`;

  const popupText = prompt;
  return (
    <div title={popupText}>
      <Border
        title={
          <div
            style={{
              maxWidth: "16em",
              overflow: "hidden",
              textOverflow: "ellipsis",
              whiteSpace: "nowrap",
            }}
          >
            {[type, titleText].filter(isDefined).join(":")}
          </div>
        }
      >
        {logText}
      </Border>
    </div>
  );
};
