import { MonitorFunction, closePopup, openPopup } from "@mjtdev/engine";
import { WaitDisplay } from "../ui/WaitDisplay";
import { useMonitorState } from "./useMonitorState";

export const setupMonitors = () => {
  const consumer: MonitorFunction = (message, state, traceId) => {
    const name = `monitor-'${traceId}'`;
    setTimeout(() => {
      if (state === "CALL") {
        const { bottom } = document.body.getBoundingClientRect();
        document.body.style.cursor = "wait";
        openPopup(
          <WaitDisplay name={name}>
            Waiting on AI: ...{message?.slice(-80)?.replace(/###.*/gm, "")}
          </WaitDisplay>,
          {
            showFrame: false,
            name,
            x: 8,
            y: bottom - 30,
          }
        );
      }
      if (state === "RESPONSE" || state === "ERROR") {
        document.body.style.cursor = null;
        closePopup(name);
      }
    }, 0);
  };
  useMonitorState.setState((state) => ({ ...state, consumer }));
};
