import { Grid, ReactCssProperties } from "@mjtdev/engine";
import {
  deleteGame,
  listGames,
  loadGame,
  saveGame,
} from "../game/useGameState";
import { LoadGameDDLB } from "./LoadGameDDLB";
import {
  getAppState,
  saveApp,
  updateAppState,
  useAppState,
} from "./useAppState";
import { openStartNewGamePopup } from "./openStartNewGamePopup";
import { waitFor } from "../ai/waitFor";
import { exportGame } from "../game/exportGame";
import { importGame } from "../game/importGame";
import { openGamePallet } from "./openGamePallet";

export const ButtonGroup = ({
  actions,
  count = 5,
  style = {},
}: {
  style?: ReactCssProperties;
  actions: Record<string, () => void>;
  count?: number;
}) => {
  const contents = Object.entries(actions).map((entry) => {
    const [key, value] = entry;
    return (
      <input
        style={{ width: "min-content", textTransform: "capitalize" }}
        onClick={value}
        key={key}
        type="button"
        value={key}
      />
    );
  });
  return (
    <Grid
      direction="column"
      gap={"0.3em"}
      cellSize={"min-content"}
      style={{ whiteSpace: "nowrap", ...style }}
      count={count}
    >
      {contents}
    </Grid>
  );
};

export const LoadSaveGameDisplay = () => {
  const { currentGame } = useAppState();
  return (
    <Grid
      style={{ whiteSpace: "nowrap" }}
      direction="column"
      cellSize={"min-content"}
    >
      <LoadGameDDLB />
      <ButtonGroup
        actions={{
          new: () => openStartNewGamePopup(),
          save: () => saveGame(),
          export: () => exportGame(),
          import: () => importGame(),
          delete: async () => {
            const { currentGame } = getAppState();
            await deleteGame(currentGame);
            const games = await listGames();
            if (games.length === 0) {
              return openStartNewGamePopup();
            }
            const game = games[0];
            await loadGame(game);
            updateAppState({ currentGame: game });
            await saveApp();
          },
        }}
      />
      <div style={{ width: "2em" }}></div>
      <button
        style={{
          borderRadius: "1em",
          backgroundColor: "black",
          color: "white",
          padding: "0.3em",

          whiteSpace: "nowrap",
          width: "min-content",
        }}
        onClick={(evt) => openGamePallet(evt)}
      >
        Menu
      </button>
    </Grid>
  );
};
