import { isUndefined } from "@mjtdev/engine";
import { getGameState, updateGameState } from "../game/useGameState";
import { openFormPopup } from "./form/openFormPopup";

export const openPromptStyleSettingsPopup = async () => {
  const { promptStyles } = getGameState();
  const resp = await openFormPopup({
    title: "Prompt Styles",
    record: promptStyles,
  });
  if (isUndefined(resp)) {
    return;
  }
  updateGameState({ promptStyles: resp });
};
