import {
  closePopup,
  openCenteredPopup
} from "@mjtdev/engine";
import { GameScene } from "./GameScene";
import { GameScenePopup } from "./GameScenePopup";


export const openGameScene = (scene: GameScene): Promise<string> => {
  return new Promise((resolve, reject) => {
    const name = openCenteredPopup(
      <GameScenePopup
        onPick={(pick) => {
          resolve(pick);
          closePopup(name);
        }}
        scene={scene} />,
      {
        onClose: () => {
          resolve(undefined);
        },
      }
    );
  });
};
