import { Randoms, isDefined } from "@mjtdev/engine";
import { findPerson } from "../../ui/findPerson";
import { Place } from "../Place";
import { advanceTime } from "../advanceTime";
import { nameOfPerson } from "../play/nameOfPerson";
import { showScene } from "./showScene";
import { GameScene } from "./GameScene";
import { QueryHistory } from "../../ai/QueryForDataParams";
import { findEntitiesByType } from "../../ui/findEntitiesByType";

export const startInteraction = async (params: {
  instruction: string;
  personIds: string[];
  place: Place;
  history?: QueryHistory;
  title?: string;
  scenes?: GameScene[];
}): Promise<GameScene[]> => {
  const {
    history = [],
    scenes = [],
    instruction,
    personIds,
    place = Randoms.pickRandom(findEntitiesByType("place")),
    title,
  } = params;
  if (isDefined(instruction)) {
    const player = findPerson(personIds[0]);

    const { pick, scene } = await showScene({
      personIds,
      place,
      history,
      instruction,
      title,
    });
    scenes.push(scene);
    if (isDefined(pick)) {
      return startInteraction({
        instruction: `the next dialog scene where ${nameOfPerson(
          player
        )} chose to ${pick}`,
        personIds,
        history: [...history, scene.lastHistoryEntry],
        place,
        scenes,
        title,
      });
    }
  }
  advanceTime();
  return scenes;
};
