import { isEmpty } from "../common/isEmpty";
import { DEFAULT_GAME_PARAMS } from "../game/DEFAULT_GAME_PARAMS";
import { startNewGame } from "../game/startNewGame";
import { openFormPopup } from "./form/openFormPopup";

export const openStartNewGamePopup = async () => {
  const defaultFields = Object.entries(DEFAULT_GAME_PARAMS).map((e) => ({
    name: e[0],
    value: String(e[1]),
  }));
  const resp = (await openFormPopup({
    title: "New Game",
    fields: [...defaultFields],
  })) as Record<string, string>;
  const name = resp?.name;
  if (isEmpty(name)) {
    return;
  }
  await startNewGame(resp);
};
