import { Arrays, Border, Grid } from "@mjtdev/engine";
import { GameScene } from "../game/interaction/GameScene";
import { PersonDisplay } from "./PersonDisplay";
import { formatText } from "../common/formatText";

export const GameSceneLogEntry = ({ scene }: { scene: GameScene }) => {
  const { place: location, text, persons, title } = scene;
  const avatars = persons.map((p, i) => (
    <PersonDisplay
      style={{ width: "2em", height: "4em", overflow: "hidden" }}
      imageStyle={{ width: "2em", height: "2em" }}
      key={i}
      person={p}
    />
  ));
  const avatarSize = Arrays.from(avatars.length).map((_) => "min-content");
  return (
    <Border
      title={
        <div style={{ whiteSpace: "nowrap" }}>
          {[title, location.name].join(", ")}
        </div>
      }
    >
      <Grid direction="column" gap="1em" cellSize={["min-content", "1fr"]}>
        <Grid cellSize={"min-content"} direction="row">
          {avatars}
        </Grid>
        <div style={{ whiteSpace: "pre" }}>{formatText(scene.text)}</div>
      </Grid>
    </Border>
  );
};
