import { findEntitiesByType } from "../ui/findEntitiesByType";
import { updatePersonTalksLike } from "./updatePersonTalksLike";

export const updatePersonTalksLikes = async (
  persons = findEntitiesByType("person")
) => {
  for (let person of persons) {
    await updatePersonTalksLike(person);
  }
};
