import { EntityType } from "../game/play/EntityType";
import { useGameState } from "../game/useGameState";
import { TypedEntity } from "./TypedEntity";

export const useEntitiesByType = <T extends EntityType>(
  type: T
): TypedEntity<T>[] => {
  const { entities } = useGameState();
  return entities.filter((e) => e.type === type) as TypedEntity<T>[];
};
